import { DarkV2Colors, isDarkTheme } from '@addressbook/components-fluentui-teams-theme';
import { makeStyles } from '../styles/makeStyles';

export const useTaskModuleStyles = /* #__PURE__*/ makeStyles(theme => ({
    root: {
        width: '100%',
        height: '100%',
        maxHeight: '100vh',
        overflow: 'hidden',
        display: 'flex',
        flexDirection: 'column',
        gap: '0.5rem',
        boxSizing: 'border-box',

        // Since the styles for task module container on Teams Desktop does not include a bottom padding, we need to
        // add some spacing, though the styles without this padding are approperiate for Teams Web.
        paddingBottom: '0.75rem',
        backgroundColor: isDarkTheme(theme.name) ? DarkV2Colors.appWhite : theme.palette.white,
    },
}));
