import { encodeDataUrl } from './data-urls';
import { isImageMime } from './mimes';

/**
 * Copies the given data to the clipboard.
 *
 * @param data - The data to copy.
 * @returns A promise that resolves when the data is copied.
 * @remarks This function doesn't use Clipboard API because some policies may prevent it from working.
 */
export async function copyToClipboard(data: Record<string, string>): Promise<void> {
    const input = document.createElement('input');
    input.style.display = 'none';

    // We have to use a dummy value, since blank value won't work in some browsers (for example, iOS Safari).
    input.value = ' ';

    try {
        document.body.appendChild(input);

        await new Promise<void>((resolve, reject) => {
            document.addEventListener(
                'copy',
                event => {
                    const dataTransfer = event.clipboardData;
                    if (!dataTransfer) return;

                    dataTransfer.clearData();
                    Object.entries(data).forEach(([type, content]) => dataTransfer.setData(type, content));

                    event.preventDefault();
                    resolve();
                },
                { once: true }
            );

            input.select();
            const successful = document.execCommand('copy');
            if (!successful) reject(new Error('Failed to copy data to clipboard'));
        });
    } finally {
        document.body.removeChild(input);
    }
}

export interface CopyImageToClipboardOptions {
    blob: Blob;
    name?: string;
}

/**
 * Copies the given image to the clipboard.
 *
 * @property blob - The image blob to copy.
 * @property name - The name of the image.
 * @returns A promise that resolves when the image is copied.
 */
export async function copyImageToClipboard({ blob, name }: CopyImageToClipboardOptions): Promise<void> {
    if (!isImageMime(blob.type)) return;

    const dataUrl = await encodeDataUrl(blob);

    const image = document.createElement('img');
    image.src = dataUrl;
    if (name) image.alt = name;

    return copyToClipboard({ [blob.type]: dataUrl, 'text/html': image.outerHTML, 'text/plain': name ?? '' });
}

export interface CopyLinkToClipboardOptions {
    url: string;
    text?: string;
    image?: string;
}

/**
 * Copies the given link to the clipboard.
 *
 * @property url - The link to copy.
 * @property text - The text of the link.
 * @property image - The image to show in the link.
 * @returns A promise that resolves when the link is copied.
 */
export function copyLinkToClipboard({ url, text, image }: CopyLinkToClipboardOptions): Promise<void> {
    const anchor = document.createElement('a');
    anchor.href = url;

    if (text || !image) {
        const textContainer = document.createElement('div');
        textContainer.textContent = text ?? url;
        anchor.appendChild(textContainer);
    }

    if (image) {
        const img = document.createElement('img');
        img.src = image;
        anchor.appendChild(img);
    }

    return copyToClipboard({ 'text/html': anchor.outerHTML, 'text/plain': url });
}
