import { IDialogStyles } from '@fluentui/react';
import { makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useDialogStyles = makeStyles<Partial<IDialogStyles>>(() => {
    return {
        main: {
            maxWidth: '600px !important',
            width: '600px !important',
            margin: '0 5px',
        },
    };
});

export const useContentStyles = makeStyles(() => ({
    footerButtons: {
        display: 'flex',
        flexDirection: 'row-reverse',
        flexWrap: 'nowrap',
        whiteSpace: 'nowrap',
        justifyContent: 'space-between',
        '.ms-Dialog-actions': {
            width: '100%',
        },
        '.ms-Dialog-actionsRight': {
            width: '100%',
        },
        '.ms-Dialog-action:first-child:nth-last-child(1)': {
            maxWidth: '100%',
            display: 'inline-block',
        },
        '.ms-Dialog-action:first-child:nth-last-child(2), .ms-Dialog-action:first-child:nth-last-child(2) ~ .ms-Dialog-action':
            {
                maxWidth: '50%',
                display: 'inline-block',
            },
        '.ms-Dialog-action:first-child:nth-last-child(3), .ms-Dialog-action:first-child:nth-last-child(3) ~ .ms-Dialog-action':
            {
                maxWidth: '33.3%',
                display: 'inline-block',
            },
        '.ms-Button': {
            maxWidth: '100%',
        },
        '.ms-Button-flexContainer': {
            maxWidth: '100%',
        },
        '.ms-Button-textContainer': {
            maxWidth: '100%',
        },
    },
    button: {
        height: '28px',
        padding: 0,
        '.ms-Button-label': {
            maxWidth: '100%',
            overflow: 'hidden',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            wordBreak: 'break-all',
            height: '1rem',
        },
        flexFlow: '1',
        flexShrink: '0',
    },
    primaryButton: {
        borderRadius: '5px',
    },
}));
