import { app, authentication, chat, dialog, menus, teamsCore } from '@microsoft/teams-js';
import { fallbackBySelector } from './fallback';
import { coerceTeamsTheme, TeamsContextTheme } from './themes';
import { ensureByTeamsInitialization, guardByTeamsInitialization } from './utils';

export function getTeamsContext(): Promise<app.Context> {
    const result = ensureByTeamsInitialization(app.getContext);
    return fallbackBySelector(result, data => data.getContext);
}

export function getAuthToken(resources: string | string[]): Promise<string> {
    const parameters = {
        resources: typeof resources === 'string' ? [resources, 'openid', 'Calls.JoinGroupCall.All'] : resources,
    };
    const result = ensureByTeamsInitialization(() => authentication.getAuthToken(parameters));
    return fallbackBySelector(result, data => data.getAuthToken);
}

export function registerOnThemeChangeHandler(handler: (theme: TeamsContextTheme) => void): Promise<void> {
    const coercedHandler: app.themeHandler = theme => handler(coerceTeamsTheme(theme));
    return ensureByTeamsInitialization(() => app.registerOnThemeChangeHandler(coercedHandler));
}

export const registerBeforeUnloadHandler = guardByTeamsInitialization(teamsCore.registerBeforeUnloadHandler);
export const notifyAppInitializationSuccess = guardByTeamsInitialization(app.notifySuccess);
export const notifyAppInitializationFailure = guardByTeamsInitialization(app.notifyFailure);
export const executeDeepLink = guardByTeamsInitialization(app.openLink);
export const openChat = guardByTeamsInitialization(chat.openChat);
// export const openChat = guardByTeamsInitialization();
export const getAuthenticationUser = guardByTeamsInitialization<authentication.UserProfile, []>(authentication.getUser);
export const setNavBarMenu = guardByTeamsInitialization(menus.setNavBarMenu);
export const resizeTaskModule = guardByTeamsInitialization(dialog.update.resize);
export const submitTask = guardByTeamsInitialization(dialog.url.submit);
export const startTask = guardByTeamsInitialization(dialog.url.open);
