import { mergeStyles, Pivot, PivotItem } from '@fluentui/react';
import React, { useCallback } from 'react';
import { usePillButtonStyles, usePillPivotStyles } from './PillPivot.styles';

export interface PillPivotProps {
    items: { key: string; text: string }[];
    defaultSelectedKey?: string;
    isMobile?: boolean;
    onSelected?: (key: string) => void;
    className?: string;
}

export const PillPivot: React.FC<PillPivotProps> = ({
    items,
    defaultSelectedKey,
    isMobile,
    onSelected,
    className,
}: PillPivotProps): JSX.Element => {
    const styles = usePillPivotStyles();
    const buttonStyles = usePillButtonStyles();

    const onLinkClick = useCallback(
        (item?: PivotItem) => {
            if (onSelected && item?.props.itemKey) {
                onSelected(item.props.itemKey);
            }
        },
        [onSelected]
    );

    return (
        <Pivot
            onLinkClick={onLinkClick}
            defaultSelectedKey={defaultSelectedKey}
            headersOnly
            styles={{
                root: mergeStyles(styles.root, isMobile && 'is-mobile', className),
                link: isMobile ? buttonStyles.mobile : buttonStyles.root,
                linkIsSelected: mergeStyles(isMobile ? buttonStyles.mobile : buttonStyles.root, 'is-active'),
                linkContent: buttonStyles.content,
            }}>
            {items.map(item => (
                <PivotItem key={item.key} itemKey={item.key} ariaLabel={item.text} headerText={item.text} />
            ))}
        </Pivot>
    );
};
