import React from 'react';
import { Modal, mergeStyleSets, IModalStyles } from '@fluentui/react';
import { useCopyRequestModalStyles } from './ContentCopyRequestModal.styles';
import { ContentCopyRequestModalContent, ContentCopyRequestProps } from './ContentCopyRequestModalContent';

export type ContentCopyRequestModalProps = ContentCopyRequestProps & {
    isOpen: boolean;
    modalStyles?: IModalStyles;
};

export const ContentCopyRequestModal: React.FC<ContentCopyRequestModalProps> = ({
    isOpen,
    modalStyles,
    ...props
}: ContentCopyRequestModalProps): JSX.Element => {
    const styles = useCopyRequestModalStyles();
    return (
        <Modal styles={mergeStyleSets(styles, modalStyles)} isOpen={isOpen} isBlocking onDismiss={props.onDismiss}>
            <ContentCopyRequestModalContent {...props} />
        </Modal>
    );
};
