import { isContrastTheme } from '@addressbook/components-fluentui-teams-theme';
import { ellipsisStyles } from '../styles/ellipsisStyles';
import { makeStyles } from '../styles/makeStyles';

export const useCommonCardStyles = makeStyles(theme => ({
    root: {
        position: 'relative',
        padding: '16px',
        width: '300px',
        height: '100px',
        boxShadow: '0px 0.3px 0.9px rgba(0, 0, 0, 0.07), 0px 1.6px 3.6px rgba(0, 0, 0, 0.11)',
        borderRadius: '4px',
        cursor: 'pointer',
        textAlign: 'start',
        background: theme.palette.white,
        border: `${isContrastTheme(theme.name) ? '0.87px' : 0} solid ${theme.palette.neutralLighter}`,
        outlineOffset: '2px',

        display: 'flex',
        boxSizing: 'border-box',
        flexDirection: 'column',
        justifyContent: 'space-between',
        alignItems: 'normal',

        color: theme.palette.neutralDark,
    },
    mobileRoot: {
        boxShadow: 'none',
        background: 'transparent',
        width: '100%',
        height: '106px',
        ':before': {
            content: '""',
            position: 'absolute',
            right: 0,
            bottom: 0,
            width: 'calc(100% - 16px)',
            borderBottom: `1px solid ${theme.palette.neutralTertiaryAlt}`,
        },
    },
    mobileRootWithoutReminder: {
        height: '64px',
        ':before': {
            width: 'calc(100% - 72px)',
        },
    },
    expandedRoot: {
        paddingInline: '18px',
        justifyContent: 'center',
    },
    hightlightedDescription: {
        color: theme.palette.redDark,
    },
    expandedIconRoot: {
        columnGap: '18px',
    },
    expandedDescription: [
        ellipsisStyles(3),
        { marginTop: '3px !important', whiteSpace: 'normal', wordBreak: 'normal', maxLines: 3 },
    ],
}));
