import React from 'react';
import { IconButton, ITextField, PrimaryButton, Stack, Text, TextField } from '@fluentui/react';
import { useContentStyles, useLinkPlaceTextFieldStyles } from './ContentCopyResponseModal.styles';

export type ContentCopyResponseProps = React.PropsWithChildren<{
    copyRequestModalCloseButton: string;
    copyRequestModalCopyButton: string;
    copyLinkSuccessInfo: string;
    generatedLink: string;
    linkPermissionSettingText: string;
    onDismiss?: () => void;
    CopySuccessIcon: JSX.Element;
    LinkPermissionSettingIcon: JSX.Element;
    copyContentToClip?: () => void;
}>;
export const ContentCopyRequestModalContent: React.FC<ContentCopyResponseProps> = ({
    copyRequestModalCloseButton,
    copyRequestModalCopyButton,
    copyLinkSuccessInfo,
    linkPermissionSettingText,
    generatedLink,
    onDismiss,
    CopySuccessIcon,
    LinkPermissionSettingIcon,
    copyContentToClip,
}) => {
    const styles = useContentStyles();
    const linkPlaceTextFieldStyles = useLinkPlaceTextFieldStyles();
    const textFieldRef = React.useRef<ITextField>(null);
    const focusTextField = (): void => {
        textFieldRef.current?.focus();
        if (copyContentToClip) {
            copyContentToClip();
        }
    };
    React.useEffect((): void => {
        textFieldRef.current?.focus();
    }, []);
    return (
        <>
            <Stack horizontal reversed role='alert' className={styles.header}>
                {onDismiss && (
                    <IconButton
                        iconProps={{ iconName: 'Cancel' }}
                        ariaLabel={copyRequestModalCloseButton}
                        title={copyRequestModalCloseButton}
                        onClick={onDismiss}
                    />
                )}
            </Stack>
            <Stack className={styles.contents}>
                <Stack horizontalAlign='center' className={styles.copySuccessIcon}>
                    {CopySuccessIcon}
                </Stack>
                <Stack horizontal horizontalAlign='center' className={styles.copyTitle}>
                    <Text block nowrap>
                        {copyLinkSuccessInfo}
                    </Text>
                </Stack>
                <Stack
                    horizontal
                    horizontalAlign='space-between'
                    className={styles.copyContent}
                    tokens={{ childrenGap: '4px' }}>
                    <TextField
                        underlined
                        readOnly
                        defaultValue={generatedLink}
                        styles={linkPlaceTextFieldStyles}
                        componentRef={textFieldRef}
                        onFocus={e => e.target.select()}
                        validateOnFocusIn
                        validateOnLoad
                    />
                    <PrimaryButton primary text={copyRequestModalCopyButton} onClick={focusTextField} />
                </Stack>
                <Stack horizontal className={styles.linkPermissionSetting}>
                    <Stack horizontalAlign='center' className={styles.linkPermissionSettingIcon}>
                        {LinkPermissionSettingIcon}
                    </Stack>
                    <Text block className={styles.linkPermissionSettingTextStyles}>
                        {linkPermissionSettingText}
                    </Text>
                </Stack>
            </Stack>
        </>
    );
};
