export interface ServiceOperationErrorInit {
    message?: string;
    request: Request;
    response: Response;
}

export class ServiceOperationError extends Error {
    request: Request;
    response: Response;

    constructor({ message, request, response }: ServiceOperationErrorInit) {
        super(message ?? response.statusText);
        this.name = this.constructor.name;
        this.request = request;
        this.response = response;
    }
}
