import { ServiceInterceptor } from '../types';
import { TestRequest, RequestSelector, urlSelector, isGet } from './utilities';

export interface PoolingInterceptorOptions {
    test?: TestRequest;
    pool: PromisePool;
}

/**
 * Create a service interceptor that pools pending promises for requests and reuses them.
 *
 * @property test Determines whether a request should be pooled.
 * @property selector A function that selects a key from a request.
 * @property pool A map of pending promises.
 * @returns An interceptor.
 */
export function createPoolingInterceptor({ test = isGet, pool }: PoolingInterceptorOptions): ServiceInterceptor {
    return async (request, proceed) => {
        if (!test(request)) return proceed(request);
        return pool.acquire(request, () => proceed(request));
    };
}

/**
 * A promise pool.
 *
 * @property acquire Acquire a promise for a request.
 */
export interface PromisePool {
    acquire(request: Request, fire: () => Promise<Response>): Promise<Response>;
}

/**
 * A promise pool that pools pending promises for requests and reuses them based on a key.
 */
export class KeyedPromisePool implements PromisePool {
    private pendings = new Map<PropertyKey, Promise<Response>>();

    /**
     * Creates an instance of MemoryPromisePool.
     *
     * @param selector A function that selects a key from a request.
     */
    constructor(private selector: RequestSelector = urlSelector) {}

    async acquire(request: Request, fire: () => Promise<Response>): Promise<Response> {
        const key = this.selector(request);

        const pending = this.pendings.get(key);
        if (pending) return (await pending).clone();

        const fired = fire();
        this.pendings.set(key, fired);

        try {
            return await fired;
        } finally {
            this.pendings.delete(key);
        }
    }
}
