export * from './src/index';

import { makeStyles as FluentUIV8MakeStyles, IStyle, Theme, UseStylesOptions } from '@fluentui/react';
/**
 * Registers a css object, optionally as a function of the theme.
 *
 * @param styleOrFunction - Either a css javascript object, or a function which takes in `ITheme`
 * and returns a css javascript object.
 *
 */
type MakeStyleFunction = <
    TStyleSet extends {
        [key in keyof TStyleSet]: IStyle;
    } = {
        [key: string]: IStyle;
    }
>(
    /**
     * a function which takes in `ITheme`
     */
    styleOrFunction: TStyleSet | ((theme: Theme) => TStyleSet)
) => (options?: UseStylesOptions) => {
    [key in keyof TStyleSet]: string;
};

export const makeStyles: MakeStyleFunction = FluentUIV8MakeStyles;
