import {
    ActionOutcome,
    AriaTelemetryClient,
    Configuration,
    EventBaseType,
    flushTelemetry,
    getClients,
    initializeTelemetryClient,
    logEvent,
    logger,
    LoggingContext,
    logUIEvent,
    PanelActionEvent,
    PanelLaunchMethod,
    PanelRegion,
    PanelViewEvent,
    ScenarioColumns,
    TelemetryContext,
    TelemetryScenarioStatus,
    updateToCompliantAriaClient,
    UserBiColumns,
    UserBIEvent,
} from '@microsoft/teams-telemetry';
import { uuid } from '@addressbook/utilities-core';
import { camelCase, groupBy } from 'lodash-es';
import { IHttpInfo, ILogger, IScenarioEventData, IScenarioLogger, ITelemetryDetails } from './common/interfaces';
import { CommonLogCategory, LoggerLevels, PanelType } from './common/types';
import { ScenarioLogger } from './ScenarioLogger';

enum AriaEventTypes {
    Trace = 'upd_trace',
    Performance = 'upd_performance',
    UserBi = 'userbi',
    NonUiEvent = 'upd_nonUiEvent',
    Scenario = 'upd_scenario',
    Http = 'upd_http',
}

const emailPattern = /([\w-\.]+)@([\w-]+\.)+[\w-]+/g;
const emailPlaceholder = '[email]';

const guidPattern = /[a-f\d]{8}(-[a-f\d]{4}){3}-[a-f\d]{12}/gi;
const guidPlaceholder = '[guid]';

/**
 * Sets up the Telemetry and hijacks the console events
 * So that we can automatically instrument the native console events
 */
export class OneDSLogger<TScenarioName extends string, TStepName extends string, TLogCategory extends string>
    implements ILogger<TScenarioName, TStepName, TLogCategory>
{
    private originalInfo: () => any;
    private originalDebug: () => any;
    private originalLog: () => any;
    private originalWarn: () => any;
    private originalError: () => any;
    private originalAssert: (test?: boolean, ...args: any[]) => any;

    public readonly sessionId: string;
    private lastUserBiViewEventTime: number;
    private activeScenarios: Record<string, IScenarioLogger<TStepName>>;

    private ariaTelemetryClient?: AriaTelemetryClient;

    private isDevelopment: boolean;
    private logTraceTypes: ('Debug' | 'Info' | 'Warning' | 'Error' | 'Exception' | 'HTTPException')[];

    // log event when promise is resolved
    private initPromise: Promise<any>;
    private initPromiseResolve: (() => void) | undefined;

    private instrumentationKey: string | undefined;

    /**
     * Logger constructor.
     *
     * @param ariaToken aria token
     * @param sessionId sessionId
     * @param delay milliseconds to wait before log event. 0 default; -1 for infinitely.
     * @param isDevelopment if true, enable console log and trace log. false default.
     */
    constructor(config?: {
        ariaToken?: string;
        sessionId?: string;
        delay?: number;
        isDevelopment?: boolean;
        logTraceTypes?: ('Debug' | 'Info' | 'Warning' | 'Error' | 'Exception' | 'HTTPException')[];
    }) {
        const { ariaToken, sessionId, delay, isDevelopment, logTraceTypes } = config || {};

        this.isDevelopment = isDevelopment || false;
        this.logTraceTypes = logTraceTypes || [];
        this.lastUserBiViewEventTime = performance.now();

        this.originalInfo = console.info.bind(console);
        this.originalDebug = console.debug ? console.debug.bind(console) : console.log.bind(console);
        this.originalLog = console.log.bind(console);
        this.originalWarn = console.warn.bind(console);
        this.originalError = console.error.bind(console);
        this.originalAssert = console.assert.bind(console);

        if (!isDevelopment) {
            // set up our functions above to use the inner function
            console.info = this.hook.bind(this, this.originalInfo, LoggerLevels.info);
            console.debug = this.hook.bind(this, this.originalDebug, LoggerLevels.debug);
            console.log = this.hook.bind(this, this.originalLog, LoggerLevels.log);
            console.warn = this.hook.bind(this, this.originalWarn, LoggerLevels.warn);
            console.error = this.hook.bind(this, this.originalError, LoggerLevels.error);
            console.assert = this.assert.bind(this);
        }

        this.initPromise = new Promise((r: any): void => (this.initPromiseResolve = r));
        if (delay === undefined || delay >= 0) {
            setTimeout((): void => this.activeLogger(), delay);
        }

        if (ariaToken) {
            this.instrumentationKey = ariaToken;
            this.initializeAria();
        }

        this.sessionId = sessionId || uuid();

        this.setCommonProperty({ Session: this.sessionId });

        this.activeScenarios = {};

        if (!isDevelopment) {
            // log all global runtime error
            window.addEventListener('error', err =>
                this.logTraceException(CommonLogCategory.GlobalException, { error: err as any as Error })
            );
            // log load performance
            window.addEventListener('load', this.logLoadPerformance.bind(this));
        }
    }

    public activeLogger(): void {
        if (this.initPromiseResolve) {
            this.initPromiseResolve();
        }
    }

    public setCompliantAriaClient(tenantId: string): Promise<void> {
        if (!this.ariaTelemetryClient) {
            return Promise.resolve();
        }

        return this.ariaTelemetryClient
            .getCompliantAriaClient(tenantId, this.getClientConfig())
            .then(client => {
                updateToCompliantAriaClient(client);
            })
            .catch(err => {
                console.error('Get ComplaintAriaClient error', err);
            });
    }

    public setCommonProperty(dataMap: Record<string, string | number | boolean | undefined>): void {
        const context = logger.getLoggerContext();
        const newContext: LoggingContext = { customProperties: context?.customProperties ?? {} };
        for (const [key, value] of Object.entries(dataMap)) {
            newContext.customProperties![key] = value as string;
        }
        logger.updateContext(newContext);
    }

    public logPerformance(
        action: string,
        source: string,
        duration: number,
        duartionDetails?: { [key: string]: number },
        dataBag?: any
    ): void {
        const logDetails = {};
        Object.entries(duartionDetails ?? {}).map(v => (logDetails[`Time.${camelCase(v[0])}`] = Math.ceil(v[1])));
        const eventProperties = {
            Action: action,
            Source: source,
            Duration: Math.ceil(duration),
            ...logDetails,
        };
        this.addDetailsToEventProperties(dataBag, eventProperties);

        this.logEvent(AriaEventTypes.Performance, eventProperties);
    }

    public logHttp(httpInfo: IHttpInfo): void {
        const eventProperties = {
            HTTP_API: httpInfo.apiName!,
            HTTP_URL: httpInfo.url,
            HTTP_ClientRequestId: httpInfo.clientRequestId,
            HTTP_CorrelationId: httpInfo.correlationId,
            HTTP_Latency: httpInfo.latency!,
            HTTP_GetTeamsContextLatency: httpInfo.getTeamsContextLatency!,
            HTTP_GetAuthTokenLatency: httpInfo.getAuthTokenLatency!,
            HTTP_ResponseCode: httpInfo.responseCode!,
            HTTP_ResponseText: httpInfo.responseText!,
            HTTP_RequestMethod: httpInfo.requestMethod,
            HTTP_Edge_Ref: httpInfo.edgeRef!,
        };

        this.logEvent(AriaEventTypes.Http, eventProperties);
    }

    public logUserBiView(
        panelLaunchMethod: PanelLaunchMethod,
        panelRegion: PanelRegion,
        panelType: PanelType,
        dataBag?: any
    ): void {
        const event: UserBIEvent<PanelViewEvent & Partial<TelemetryContext>> = {
            eventName: EventBaseType.PanelView,
            properties: {
                [UserBiColumns.PanelRegion]: panelRegion,
                [UserBiColumns.PanelType]: panelType,
                [UserBiColumns.PanelLaunchMethod]: panelLaunchMethod,
                [UserBiColumns.PanelUri]: window.location.hash,
            },
        };
        this.lastUserBiViewEventTime = performance.now();
        this.addDetailsToEventProperties(dataBag, event.properties);

        this.logUIEvent<PanelViewEvent>(event);
    }

    public logUserBiAction(
        scenarioName: TScenarioName,
        scenarioType: string,
        moduleName: string,
        dataBag?: any,
        pageLeave?: boolean
    ): void {
        const event: UserBIEvent<any> = {
            eventName: EventBaseType.PanelAction,
            properties: {
                [UserBiColumns.Scenario]: scenarioName,
                [UserBiColumns.ScenarioType]: camelCase(scenarioType),
                [UserBiColumns.ModuleName]: camelCase(moduleName),
                [UserBiColumns.PanelUri]: window.location.hash,
                ['Panel.Path']: window.location.hash.replace(guidPattern, guidPlaceholder),
                ['DataBag.PageElapsed']: performance.now() - this.lastUserBiViewEventTime,
                ['DataBag.PageLeave']: !!pageLeave,
            },
        };

        this.addDetailsToEventProperties(dataBag, event.properties);
        this.logUIEvent<PanelActionEvent>(event);
    }

    public logNonUiTelemetry(nonUiComponent: string, outcome: ActionOutcome, details?: any): void {
        const eventProperties = {
            SourceComponent: nonUiComponent,
            Event: outcome,
        };

        this.addDetailsToEventProperties(details, eventProperties);

        this.logEvent(AriaEventTypes.NonUiEvent, eventProperties);
    }

    public createScenario(name: string, dataBag?: any): IScenarioLogger<TStepName> {
        if (!name) {
            throw new Error('Missing scenario name.');
        }

        if (this.activeScenarios[name] != null) {
            console.warn(`trying to create a scenario: ${name} that already exists`);
            return this.activeScenarios[name];
        }

        const newScenario = new ScenarioLogger(this, name, dataBag);
        this.activeScenarios[name] = newScenario;
        return newScenario;
    }

    public findScenario(name: string): IScenarioLogger<TStepName> | null {
        return this.activeScenarios[name] ? this.activeScenarios[name] : null;
    }

    public markScenario(
        scenarioName: TScenarioName,
        step: TStepName,
        status?: TelemetryScenarioStatus,
        eventData?: Partial<IScenarioEventData>
    ): void {
        const scenario = this.findScenario(scenarioName);

        if (scenario) {
            scenario.mark(step, status, eventData);
        }
    }

    public stopScenario(scenarioName: string, eventData?: Partial<IScenarioEventData>): void {
        const scenario = this.findScenario(scenarioName);

        if (scenario) {
            scenario.stop(eventData);
        }
    }

    public failScenario(scenarioName: string, eventData?: Partial<IScenarioEventData>): void {
        const scenario = this.findScenario(scenarioName);

        if (scenario) {
            scenario.fail(eventData);
        }
    }

    public logScenario(name: string, eventData: IScenarioEventData, completeScenario: boolean): void {
        const eventProperties = { [ScenarioColumns.ScenarioName]: name };
        Object.keys(eventData).forEach((key: string): void => {
            if (key === 'dataBag') {
                this.addDetailsToEventProperties(eventData.dataBag, eventProperties);
            } else {
                eventProperties[key.charAt(0).toUpperCase() + key.slice(1)] = eventData[key];
            }
        });

        this.logEvent(AriaEventTypes.Scenario, eventProperties);

        if (completeScenario) {
            delete this.activeScenarios[name];
        }
    }

    public markScenarioComplete(name: string): void {
        if (this.activeScenarios[name] != null) {
            delete this.activeScenarios[name];
        }
    }

    public hook(_stream: null | ((str: string, args?: any[]) => any), prefix: string, ...args: any[]): void {
        let message = '';

        // more data args are passed, stringify and record
        for (const arg of args) {
            try {
                const formattedArg = JSON.stringify(arg, null, 2);
                message += '\r\n' + formattedArg;
            } catch (err) {
                console.warn(`Error stringifying console logs, because: ${err}`);
            }
        }

        switch (prefix) {
            case LoggerLevels.info:
            case LoggerLevels.debug:
            case LoggerLevels.log:
                if (this.isDevelopment) {
                    this.logTraceInternal(prefix, 'console', message);
                }
                break;
            case LoggerLevels.warn:
            case LoggerLevels.error:
                this.logTraceInternal(prefix, 'console', message);
                break;
        }
    }

    public logTraceDebug(logCategory: TLogCategory | CommonLogCategory, message: string): void {
        // If Debug logs are disabled for this user, don't do anything
        if (!this.isLogTraceDebugEnabled()) {
            return;
        }

        this.logTraceInternal(LoggerLevels.debug, logCategory, message);
    }

    public logTraceInfo(logCategory: TLogCategory | CommonLogCategory, message: string): void {
        // If Info logs are disabled for this user, don't do anything
        if (!this.isLogTraceInfoEnabled()) {
            return;
        }

        this.logTraceInternal(LoggerLevels.info, logCategory, message);
    }

    public logTraceWarning(logCategory: TLogCategory | CommonLogCategory, message: string): void {
        // If Warning logs are disabled for this user, don't do anything
        if (!this.isLogTraceWarningEnabled()) {
            return;
        }

        this.logTraceInternal(LoggerLevels.warn, logCategory, message);
    }

    public logTraceError(logCategory: TLogCategory | CommonLogCategory, message: string): void {
        // If Error logs are disabled for this user, don't do anything
        if (!this.isLogTraceErrorEnabled()) {
            return;
        }

        this.logTraceInternal(LoggerLevels.error, logCategory, message);
    }

    public logTraceException(
        logCategory: TLogCategory | CommonLogCategory,
        options: ITelemetryDetails,
        dataBag?: any
    ): void {
        if (this.isDevelopment) {
            console.log(logCategory, JSON.stringify(options));
        }

        // If Exception logs are disabled for this user in production, don't do anything
        if (!this.isLogTraceExceptionEnabled()) {
            return;
        }

        this.logTraceInternal(LoggerLevels.error, logCategory, options.message, options, dataBag);
    }

    public logTraceHttpException(httpAPI: string, message: string, httpStatusCode: number): void {
        const messageWithStatusCode = `{${httpAPI}} API call failed with message {${message}}, statusCode {${httpStatusCode}}`;
        if (this.isDevelopment) {
            console.log(messageWithStatusCode);
        }

        if (!this.isLogTraceHTTPExceptionEnabled()) {
            return;
        }

        this.logTraceInternal(LoggerLevels.error, CommonLogCategory.HTTPException, messageWithStatusCode);
    }

    public flushLogs(callback?: (() => void) | undefined): void {
        const telemetryClients = getClients();
        telemetryClients.forEach(telemetryClient => {
            if (telemetryClient.isInitialized()) {
                telemetryClient.flush(callback);
            }
        });
    }

    public flushAndTeardown(): void {
        flushTelemetry();
    }

    // Check if debug-level logs are allowed to pass through
    private isLogTraceDebugEnabled(): boolean {
        return this.logTraceTypes.includes('Debug');
    }

    // Check if info-level logs are allowed to pass through
    private isLogTraceInfoEnabled(): boolean {
        return this.logTraceTypes.includes('Info');
    }

    // Check if warning-level logs are allowed to pass through
    private isLogTraceWarningEnabled(): boolean {
        return this.logTraceTypes.includes('Warning');
    }

    // Check if error-level logs are allowed to pass through
    private isLogTraceErrorEnabled(): boolean {
        return this.logTraceTypes.includes('Error');
    }

    // Check if exception are allowed to pass through
    private isLogTraceExceptionEnabled(): boolean {
        return this.logTraceTypes.includes('Exception');
    }

    // Check if HTTP exception are allowed to pass through
    private isLogTraceHTTPExceptionEnabled(): boolean {
        return this.logTraceTypes.includes('HTTPException');
    }

    private logTraceInternal(
        level: LoggerLevels,
        logCategory: TLogCategory | string,
        message?: string,
        options?: ITelemetryDetails,
        dataBag?: any
    ): void {
        const eventProperties: { [key: string]: string } = {
            LogLevel: level,
            LogCategory: logCategory,
        };

        if (message) {
            eventProperties.Message = message;
        }

        this.addDetailsToEventProperties(dataBag, eventProperties);

        if (options) {
            if (options.error) {
                if (options.error.stack) {
                    eventProperties.CallStack = options.error.stack;
                }

                if (options.error.name) {
                    eventProperties.ExceptionType = options.error.name;
                }

                if (options.error.message) {
                    eventProperties.ExceptionMessage = options.error.message;
                }

                const code = (options.error as { code?: unknown }).code;
                if (typeof code === 'string') {
                    eventProperties.ExceptionCode = code;
                }
            }

            if (options.details) {
                eventProperties.Details = options.details;
            }

            if (options.method) {
                eventProperties.Method = options.method;
            }
        }

        this.logEvent(AriaEventTypes.Trace, eventProperties);
    }

    private assert(test?: boolean, ...args: any[]): void {
        this.originalAssert(test, args);
        if (test === false) {
            this.hook(null, 'err', args);
        }
    }

    private addDetailsToEventProperties(
        details: any,
        eventProperties: {
            [key: string]: string | number | boolean | null;
        }
    ): void {
        if (details) {
            const typeOfDetails = typeof details;
            if (typeOfDetails === 'string' || typeOfDetails === 'boolean' || typeOfDetails === 'number') {
                eventProperties['DataBag.info'] = details;
            } else {
                Object.keys(details).forEach((key: string): void => {
                    const typeOfDetail = typeof details[key];
                    const eventKey = `DataBag.${key}`;
                    if (typeOfDetail === 'string' || typeOfDetail === 'boolean' || typeOfDetail === 'number') {
                        eventProperties[eventKey] = details[key];
                    } else {
                        eventProperties[eventKey] = JSON.stringify(details[key]);
                    }
                });
            }
        }
    }

    private logEvent(
        eventName: string,
        properties: {
            [key: string]: string | number | boolean;
        }
    ): void {
        properties['AppInfo.ElapsedTime'] = Math.ceil(performance.now());
        this.initPromise.then((): void => {
            logEvent(eventName, properties);
        });
    }

    private logUIEvent<T>(event: UserBIEvent<T & Partial<TelemetryContext>>): void {
        (event.properties as any)['AppInfo.ElapsedTime'] = Math.ceil(performance.now());
        this.initPromise.then((): void => {
            logUIEvent(event);
        });
    }

    private getClientConfig(): Configuration {
        const config: Configuration = {
            instrumentationKey: this.instrumentationKey,
            channelConfiguration: {
                // Post channel configuration
                eventsLimitInMem: 50,
            },
            webAnalyticsConfiguration: {
                // Web Analytics Plugin configuration
                autoCapture: {
                    scroll: false,
                    pageView: false,
                    onLoad: false,
                    onUnload: false,
                    click: false,
                    jsError: false,
                },
            },
        };
        return config;
    }

    private initializeAria(): void {
        this.ariaTelemetryClient = new AriaTelemetryClient(this.getClientConfig(), this.isDevelopment);
        initializeTelemetryClient(this.ariaTelemetryClient, this.isDevelopment);
    }

    private logLoadPerformance(): void {
        setTimeout(() => {
            let perfDetails: { [key: string]: number } = {};

            // https://developer.mozilla.org/en-US/docs/Web/API/PerformancePaintTiming
            performance.getEntriesByType('paint').forEach(p => (perfDetails[p.name] = p.startTime));

            // https://developer.mozilla.org/en-US/docs/Web/API/PerformanceResourceTiming
            const resPerfs = performance.getEntriesByType('resource') as PerformanceResourceTiming[];
            const resourceDetails = groupBy(resPerfs, res => res.initiatorType);

            Object.entries(resourceDetails).map(([type, value]) => {
                const duration = Math.max(...value.map(j => j.responseEnd)) - Math.min(...value.map(j => j.startTime));
                this.logPerformance(`resource-${type}`, window.location.href, duration, undefined, {
                    details: value.map(p => ({
                        name: this.formatUrl(p.name),
                        duration: Math.ceil(p.duration),
                        size: p.transferSize,
                    })),
                });
            });

            const js = resPerfs.filter(ele => ele.initiatorType === 'script');
            perfDetails['download-script'] =
                Math.max(...js.map(j => j.responseEnd)) - Math.min(...js.map(j => j.startTime));

            // https://developer.mozilla.org/en-US/docs/Web/API/PerformanceNavigationTiming
            // https://developer.mozilla.org/en-US/docs/Web/Performance/Navigation_and_resource_timings
            const timeInfo = performance.getEntriesByType('navigation')?.[0] as PerformanceNavigationTiming;
            if (!timeInfo) {
                return;
            }
            perfDetails['page-ttfb'] = timeInfo.responseStart - timeInfo.startTime;
            const loadDuration = timeInfo.loadEventStart - timeInfo.startTime;
            perfDetails['page-loading'] = loadDuration;

            perfDetails = {
                ...perfDetails,
                Cache: timeInfo.domainLookupStart - timeInfo.fetchStart,
                Dns: timeInfo.domainLookupEnd - timeInfo.domainLookupStart,
                Connect: timeInfo.connectEnd - timeInfo.connectStart,
                Request: timeInfo.responseStart - timeInfo.requestStart,
                Download: timeInfo.responseEnd - timeInfo.responseStart,
                Dom: timeInfo.domComplete - timeInfo.responseEnd,
                Contentload: timeInfo.loadEventEnd - timeInfo.loadEventStart,
                DOMContentLoaded: timeInfo.domContentLoadedEventEnd - timeInfo.domContentLoadedEventStart,
                Redirect: timeInfo.redirectEnd - timeInfo.redirectStart,
            };

            this.logPerformance('performance-timing', window.location.href, loadDuration, perfDetails);
        }, 0);
    }

    private formatUrl(url: string): string {
        // remove same url prefix
        // eg. https://reports.teams.microsoft.com/dist/js/runtime~main.1234.js to runtime~main.1234.js
        if (url.startsWith(location.origin)) {
            url = url.split('/').pop() ?? '';
        }
        // remove email
        return decodeURIComponent(url).replace(emailPattern, emailPlaceholder);
    }
}
