import { ServiceInterceptor } from '../types';

/**
 * A authenticate function to be called when a request is made, to allocate an access token.
 *
 * @param request The request to allocate the token for.
 * @returns An access token to be attached to the request, or an empty string if no token is available.
 */
export type Authenticate = (request: Request) => Promise<string>;

/**
 * Create a service interceptor to authorize requests.
 *
 * @param authenticate The authenticate function.
 * @returns An interceptor.
 */
export function createAuthorizationInterceptor(authenticate: Authenticate): ServiceInterceptor {
    return async (request, proceed) => {
        const token = await authenticate(request);
        const authorizedRequest = token ? authorize(request, token) : request;
        return proceed(authorizedRequest);
    };
}

function authorize(request: Request, token: string): Request {
    const authorizedRequest = new Request(request);
    authorizedRequest.headers.set('Authorization', `Bearer ${token}`);
    return authorizedRequest;
}
