import { mergeStyles } from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';
import React from 'react';
import { PeopleCoin } from '../PeopleCoin';
import { PeopleSelection, PeopleSelectionType } from '../types';
import { usePeopleViewStyles } from './PeopleView.styles';
import { FormatNames, PeopleViewProps } from './PeopleView.types';

const defaultFormatNames: FormatNames = (names: string[]) => ({ normal: names.join(', ') });

export const PeopleView: React.FC<PeopleViewProps> = ({
    label,
    selections,
    onRetrieveIcon,
    formatNames = defaultFormatNames,
    className,
    ...props
}) => {
    const { normal: names, compact: compactNames } = React.useMemo(
        () => formatNames(selections.map(selection => selection.name)),
        [selections, formatNames]
    );

    const aggregatedSelection = React.useMemo<PeopleSelection>(
        () =>
            selections.length === 1
                ? selections[0]
                : { type: PeopleSelectionType.Unspecific, id: 'aggregated', name: names, icon: 'People' },
        [names, selections]
    );

    const labelId = useId('label');
    const styles = usePeopleViewStyles();
    return (
        <div className={mergeStyles(styles.root, className)} {...props}>
            <PeopleCoin selection={aggregatedSelection} onRetrieveIcon={onRetrieveIcon} coinSize={32} />
            <div className={styles.textContainer}>
                <div className={styles.names} title={names} aria-labelledby={labelId}>
                    {compactNames ?? names}
                </div>
                <div className={styles.label} id={labelId}>
                    {label}
                </div>
            </div>
        </div>
    );
};
