import { applyInterceptor, composeInterceptors } from '@addressbook/utilities-interceptors';
import { ServiceOperationError } from './error';
import { Fetch, ServiceInterceptor } from './types';

export interface ServiceOperationOptions {
    baseUrl?: string;
    fetch?: Fetch;
    interceptors?: ServiceInterceptor[];
}

export class ServiceOperation {
    private baseUrl;
    private fetch;

    constructor({ baseUrl, fetch = window.fetch, interceptors = [] }: ServiceOperationOptions = {}) {
        this.baseUrl = baseUrl;
        this.fetch = interceptors.length ? applyInterceptor(composeInterceptors(interceptors), fetch) : fetch;
    }

    async request(url: string | URL, init?: RequestInit): Promise<Response> {
        const request = new Request(new URL(url, this.baseUrl), init);
        const response = await this.fetch(request);
        if (!response.ok) throw new ServiceOperationError({ request, response });
        return response;
    }

    async requestJson<T = unknown>(url: string | URL, init?: RequestInit): Promise<T> {
        const response = await this.request(url, init);
        return response.json();
    }

    async requestBlob(url: string | URL, init?: RequestInit): Promise<Blob> {
        const response = await this.request(url, init);
        return response.blob();
    }
}
