import { ServiceInterceptor } from '../types';
import { isGetAndOk, RequestSelector, TestRequestAndResponse, urlSelector } from './utilities';

export interface CachingInterceptorOptions {
    test?: TestRequestAndResponse;
    cache: ServiceCache;
}

/**
 * Create a service interceptor that caches responses.
 *
 * @property test Determines whether a request and response should be cached.
 * @property cache The cache used to store responses.
 * @returns An interceptor.
 */
export function createCachingInterceptor({ test = isGetAndOk, cache }: CachingInterceptorOptions): ServiceInterceptor {
    return async (request, proceed) => {
        if (!test(request)) return proceed(request);

        const cachedResponse = await cache.get(request);
        if (cachedResponse) return cachedResponse;

        const response = await proceed(request);
        if (!test(request, response)) return response;

        await cache.set(request, response.clone());
        return response;
    };
}

/**
 * A service cache.
 *
 * @property get Gets a cached response by the request.
 * @property set Sets a cached response for the request.
 */
export interface ServiceCache {
    get(request: Request): Promise<Response | undefined>;
    set(request: Request, response: Response): Promise<void>;
}

/**
 * An memory service cache that stores responses in a Map.
 */
export class MemoryServiceCache implements ServiceCache {
    private cache = new Map<PropertyKey, Response>();

    /**
     * Creates an instance of MemoryServiceCache.
     *
     * @param selector A function that selects a key from a request.
     */
    constructor(private selector: RequestSelector = urlSelector) {}

    async get(request: Request): Promise<Response | undefined> {
        return this.cache.get(this.selector(request))?.clone();
    }

    async set(request: Request, response: Response): Promise<void> {
        this.cache.set(this.selector(request), response);
    }
}
