import React from 'react';
import { Modal, mergeStyleSets, IModalStyles } from '@fluentui/react';
import { useCopyResponseModalStyles } from './ContentCopyResponseModal.styles';
import { ContentCopyRequestModalContent, ContentCopyResponseProps } from './ContentCopyResponseModalContent';

export type ContentCopyResponseModalProps = ContentCopyResponseProps & {
    isOpen: boolean;
    modalStyles?: IModalStyles;
};

export const ContentCopyResponseModal: React.FC<ContentCopyResponseModalProps> = ({
    isOpen,
    modalStyles,
    ...props
}: ContentCopyResponseModalProps): JSX.Element => {
    const styles = useCopyResponseModalStyles();
    return (
        <Modal styles={mergeStyleSets(styles, modalStyles)} isOpen={isOpen} isBlocking onDismiss={props.onDismiss}>
            <ContentCopyRequestModalContent {...props} />
        </Modal>
    );
};
