import { mergeStyles } from '@fluentui/react';
import { rem } from '@addressbook/components-fluentui-teams-theme';
import React from 'react';
import { PeopleCoin } from '../PeopleCoin';
import { PeopleSelection, RetrieveIcon } from '../types';
import { usePeoplePileStyles } from './PeoplePile.styles';

/**
 * The props for the PeoplePile component, which renders a pile of people in a horizontal layout.
 *
 * @property selections The people to display.
 * @property onRetrieveIcon A function to retrieve the icon for each person.
 * @property maxVisible The maximum number of people to display before showing an overflow count.
 * @property compact If true, the pile will be compact, that is, icons will overlap each other.
 * @property coinSize The size of the coins.
 * @property showOnlyName If true, only the name of the first person will be displayed.
 * @property overflowTitle The title for the overflow count.
 */
export interface PeoplePileProps extends React.HTMLAttributes<HTMLDivElement> {
    selections: PeopleSelection[];
    onRetrieveIcon?: RetrieveIcon;
    maxVisible?: number;
    compact?: boolean;
    coinSize?: number;
    showOnlyName?: boolean;
    overflowTitle?: string;
}

export const PeoplePile: React.FC<PeoplePileProps> = ({
    selections,
    onRetrieveIcon,
    maxVisible = Infinity,
    compact,
    coinSize = 24,
    showOnlyName,
    overflowTitle,
    className,
    ...props
}) => {
    const [visibleSelections, overflowCount] = React.useMemo(() => {
        const visibleSelections = selections.slice(0, maxVisible);
        const overflowCount = Math.max(selections.length - maxVisible, 0);
        return [visibleSelections, overflowCount];
    }, [maxVisible, selections]);

    const onlyNameText = React.useMemo(
        () => showOnlyName && !overflowCount && selections.length === 1 && selections[0].name,
        [overflowCount, selections, showOnlyName]
    );

    const styles = usePeoplePileStyles();

    const overflow = React.useMemo(
        () =>
            overflowCount > 0 && (
                <div
                    className={mergeStyles(styles.overflow, compact && styles.compactOverflow, {
                        fontSize: rem(Math.min(coinSize * 0.5, 10)),
                    })}
                    title={overflowTitle}>
                    {overflowCount}
                </div>
            ),
        [overflowCount, overflowTitle, styles, compact, coinSize]
    );

    const onlyName = React.useMemo(
        () =>
            onlyNameText && (
                <div
                    className={mergeStyles(styles.onlyName, {
                        fontSize: rem(Math.min(coinSize * 0.75, 14)),
                        margin: `0 ${rem(2 + coinSize * 0.25)}`,
                    })}
                    title={props.title ? undefined : onlyNameText}>
                    {onlyNameText}
                </div>
            ),
        [coinSize, onlyNameText, props.title, styles.onlyName]
    );

    return (
        <div className={mergeStyles(styles.root, className)} {...props}>
            <div className={mergeStyles(styles.coins, compact && styles.compactCoins)} role='list'>
                {visibleSelections.map(selection => (
                    <PeopleCoin
                        key={selection.id}
                        role='listitem'
                        selection={selection}
                        onRetrieveIcon={onRetrieveIcon}
                        coinSize={coinSize}
                        preventNameAsTitle={Boolean(props.title)}
                    />
                ))}
            </div>
            {overflow}
            {onlyName}
        </div>
    );
};
