import { useBoolean } from '@fluentui/react-hooks';
import React from 'react';
import Sheet from 'react-modal-sheet';
import { FocusTrapZone } from '@fluentui/react';
import {
    useMobileSheetBackdropStyles,
    useMobileSheetContainerStyles,
    useMobileSheetStyles,
} from './MobileSheet.styles';

export interface MobileSheetProps {
    onDismiss?: () => void;
    content: React.ReactNode;
    snapPoints?: number[];
    initialSnap?: number;
}
export const MobileSheet: React.FC<MobileSheetProps> = ({
    onDismiss,
    content,
    snapPoints,
    initialSnap,
}: MobileSheetProps) => {
    const styles = useMobileSheetStyles();
    const containerStyles = useMobileSheetContainerStyles();
    const backdropStyles = useMobileSheetBackdropStyles();
    const [isOpen, { setFalse: onClose }] = useBoolean(true);

    const keyEventName = 'keydown';
    React.useEffect(() => {
        const handleKeyDown = (event: KeyboardEvent): void => {
            if (event.key === 'Escape') {
                onClose();
            }
        };
        document.addEventListener(keyEventName, handleKeyDown, true);

        return () => {
            document.removeEventListener(keyEventName, handleKeyDown, true);
        };
    }, [onClose]);

    return (
        <Sheet
            snapPoints={snapPoints}
            initialSnap={initialSnap}
            isOpen={isOpen}
            onClose={onClose}
            onCloseEnd={onDismiss}
            detent='content-height'>
            <Sheet.Container style={containerStyles}>
                <FocusTrapZone>
                    <Sheet.Header>
                        <div className={styles.header}>
                            <div className={styles.handler} />
                        </div>
                    </Sheet.Header>
                    <Sheet.Content>
                        <div className={styles.content}>{content}</div>
                    </Sheet.Content>
                </FocusTrapZone>
            </Sheet.Container>
            <Sheet.Backdrop style={backdropStyles} onTap={onClose} />
        </Sheet>
    );
};
