import {
    DefaultButton,
    IButtonProps,
    PrimaryButton,
    Dialog,
    DialogFooter,
    DialogType,
    IDialogProps,
    mergeStyles,
} from '@fluentui/react';
import * as React from 'react';
import { useDialogStyles, useContentStyles } from './ConfirmationDialog.styles';

export interface ConfirmationDialogProps {
    isOpen: boolean;
    title: string | JSX.Element;
    subText?: string;
    // Custom content
    content?: JSX.Element;
    // Method used to dismiss the dialog
    dismiss?: () => void;
    // Possible actions for selection
    actions: IButtonProps[];
    showDarkOverlay?: boolean;
    showCloseButton?: boolean;
}

export const ConfirmationDialog: React.FC<ConfirmationDialogProps> = ({
    title,
    subText,
    isOpen,
    content,
    dismiss,
    actions,
    showDarkOverlay,
    showCloseButton = true,
}: ConfirmationDialogProps): JSX.Element => {
    const contentStyles = useContentStyles();
    const buttons =
        actions &&
        actions.map((buttonProps: IButtonProps, index: number): JSX.Element => {
            return buttonProps.primary ? (
                <PrimaryButton
                    className={mergeStyles(contentStyles.button, contentStyles.primaryButton)}
                    {...buttonProps}
                    key={index}
                />
            ) : (
                <DefaultButton className={contentStyles.button} {...buttonProps} key={index} />
            );
        });

    const dialogProps: IDialogProps = {
        hidden: !isOpen,
        onDismiss: dismiss,
        styles: useDialogStyles(),
        dialogContentProps: {
            type: DialogType.normal,
            title,
            subText,
            showCloseButton,
        },
        modalProps: {
            isDarkOverlay: !!showDarkOverlay,
            overlay:
                !showDarkOverlay &&
                ({
                    isDarkThemed: false,
                    styles: {
                        root: {
                            background: 'none',
                        },
                    },
                } as any),
            isBlocking: true,
        },
    };

    return (
        <Dialog {...dialogProps}>
            {content}
            <DialogFooter className={contentStyles.footerButtons}>{buttons}</DialogFooter>
        </Dialog>
    );
};
