import { IPersonaProps } from '@fluentui/react';
import React from 'react';
import { PeopleCoin } from '../PeopleCoin';
import { PeopleSelection, PeopleSelectionType, WithSelection } from '../types';
import { PeopleSelectProps } from './PeopleSelect.types';

/**
 * Extracts the selection from persona props.
 *
 * @param props The persona props.
 * @returns The selection.
 */
export function extractSelectionFromPersonaProps(props: IPersonaProps): PeopleSelection {
    return (props as WithSelection<IPersonaProps>).selection;
}

/**
 * Normalizes filter text.
 *
 * @param filter The filter text to normalize.
 * @returns The normalized filter text.
 * @example normalizeFilter('') // ''
 * @example normalizeFilter('  ') // ''
 * @example normalizeFilter('Ruijun Gao') // 'ruijun gao'
 * @example normalizeFilter('  Ruijun Gao  ') // 'ruijun gao'
 */
export function normalizeFilter(filter: string): string {
    return filter.trim().toLowerCase();
}

/**
 * Filters suggestions based on selections.
 *
 * @param selections The selections to filter suggestions against.
 * @param suggestions The suggestions to filter.
 * @returns The filtered suggestions.
 * @example filterSuggestions([], []) // []
 * @example filterSuggestions([{ id: '1', name: 'Ruijun Gao' }], [{ id: '1', name: 'Ruijun Gao' }]) // []
 * @example filterSuggestions([{ id: '1', name: 'Ruijun Gao' }], [{ id: '2', name: 'Ruijun Gao' }]) // [{ id: '1', name: 'Ruijun Gao' }]
 */
export function filterSuggestions(suggestions: PeopleSelection[], selections: PeopleSelection[]): PeopleSelection[] {
    return suggestions.filter(suggestion => !selections.some(selection => selection.id === suggestion.id));
}

/**
 * Resolves the items of a selection in-place if it is resolvable and not resolved.
 *
 * @param selection The selection to resolve.
 * @returns A promise that resolves when the selection is resolved.
 */
export async function resolvePeopleSelectionItems(selection: PeopleSelection): Promise<void> {
    if (selection.type === PeopleSelectionType.User || !selection.resolveItems) return;

    if (!selection.items) selection.items = selection.resolveItems();
    if (!Array.isArray(selection.items)) selection.items = await selection.items;
}

/**
 * Expands a selection in selections into multiple selections if it is expandable.
 *
 * @param selections The selections.
 * @param selection The selection to expand.
 * @returns The new selections if the selection is expanded, otherwise undefined.
 */
export function tryExpandPeopleSelection(
    selections: PeopleSelection[],
    selection: PeopleSelection
): PeopleSelection[] | undefined {
    if (selection.type === PeopleSelectionType.User || !selection.expandable || !Array.isArray(selection.items)) return;

    const index = selections.indexOf(selection);
    if (index < 0) return;

    const newSelections = [...selections];
    const absentSelections = filterSuggestions(selection.items, selections);
    const patchedSelections = absentSelections.map(absentSelection => ({
        ...absentSelection,
        unremovable: absentSelection.unremovable || selection.unremovable,
    }));

    newSelections.splice(index, 1, ...patchedSelections);
    return newSelections;
}

export type UseMapPeopleSelectionToPersonaOptions = Pick<PeopleSelectProps, 'onRetrieveIcon' | 'describeSuggestion'>;
export type MapPeopleSelectionToPersonaProps = (selection: PeopleSelection) => WithSelection<IPersonaProps>;

/**
 * A hook that creates a function that maps a selection to a persona props.
 * The function will memoize the result based on the describeSuggestion function.
 *
 * @param describeSuggestion A function that describes a suggestion, used to generate the secondary text.
 * @returns A function that maps a selection to a persona props.
 */
export function useMapPeopleSelectionToPersonaProps({
    onRetrieveIcon,
    describeSuggestion,
}: UseMapPeopleSelectionToPersonaOptions): MapPeopleSelectionToPersonaProps {
    return React.useCallback(
        selection => ({
            key: selection.id,
            text: selection.name,
            secondaryText: describeSuggestion?.(selection),
            selection,
            onRenderPersonaCoin: props => (
                <PeopleCoin {...props} selection={selection} onRetrieveIcon={onRetrieveIcon} coinSize={32} />
            ),
        }),
        [describeSuggestion, onRetrieveIcon]
    );
}
