export type TeamsContextTheme = 'default' | 'dark' | 'contrast';

const knownThemes: TeamsContextTheme[] = ['default', 'dark', 'contrast'];
const fallbackTheme: TeamsContextTheme = 'default';

/**
 * Coerce the theme to a known value.
 *
 * @param theme The theme to coerce.
 * @returns The coerced theme.
 */
export function coerceTeamsTheme(theme: string): TeamsContextTheme {
    return knownThemes.find(knownTheme => knownTheme === theme) ?? fallbackTheme;
}
