import {
    Icon,
    IIconProps,
    ImageFit,
    IPersonaCoinProps,
    IPersonaSharedProps,
    PersonaCoin,
    useTheme,
} from '@fluentui/react';
import { isAbsoluteUrl } from '@addressbook/utilities-core';
import React from 'react';
import { RetrieveIcon, WithSelection } from '../types';
import { usePeopleCoinStyles } from './PeopleCoin.styles';
import { useIcon } from './PeopleCoin.utils';

/**
 * The props for the PeopleCoin component, which renders a person as a coin.
 *
 * @property selection The person to display.
 * @property onRetrieveIcon A function to retrieve the icon for the person.
 */
export interface PeopleCoinProps extends WithSelection<IPersonaSharedProps> {
    onRetrieveIcon?: RetrieveIcon;
    preventNameAsTitle?: boolean;
}

export const PeopleCoin: React.FC<PeopleCoinProps> = ({ selection, onRetrieveIcon, preventNameAsTitle, ...props }) => {
    const theme = useTheme();
    const styles = usePeopleCoinStyles();

    const icon = useIcon(selection, onRetrieveIcon);

    const propsInternal = React.useMemo<IPersonaCoinProps>(() => {
        const baseProps = { text: selection.name, title: preventNameAsTitle ? undefined : selection.name, ...props };
        if (!icon) return baseProps;

        const iconProps: IIconProps = isAbsoluteUrl(icon)
            ? {
                  imageProps: { src: icon, imageFit: ImageFit.cover },
                  style: props.coinSize
                      ? { boxSizing: 'border-box', width: props.coinSize, height: props.coinSize }
                      : {},
              }
            : {
                  iconName: icon,
                  style: props.coinSize
                      ? { boxSizing: 'border-box', fontSize: props.coinSize, padding: props.coinSize * 0.1875 }
                      : {},
              };

        return {
            ...baseProps,
            onRenderInitials: props => <Icon className={styles.icon} title={props?.title} {...iconProps} />,
            initialsColor: theme.palette.themeLight,
            initialsTextColor: theme.palette.themeDark,
        };
    }, [
        icon,
        preventNameAsTitle,
        props,
        selection.name,
        styles.icon,
        theme.palette.themeDark,
        theme.palette.themeLight,
    ]);

    return <PersonaCoin {...props} {...propsInternal} />;
};
