import {
    ActionButton,
    Checkbox,
    DefaultButton,
    IButtonProps,
    IconButton,
    mergeStyles,
    PrimaryButton,
} from '@fluentui/react';
import React, { MutableRefObject } from 'react';
import { CommonIcon, CommonIconProps } from '../CommonIcon';
import { CommonLoadingIndicator } from '../CommonLoadingIndicator';
import { useContentStyles, useShareSubmissionCardConsentStyles } from './CommonModal.styles';

export type ModalContentProps = React.PropsWithChildren<{
    /**
     * set header icon, title and description
     */
    headerProps?: CommonIconProps;
    /**
     * props for the PrimaryButton
     */
    primaryBtnProps?: IButtonProps;
    /**
     * props for the ActionButton
     */
    secondaryBtnProps?: IButtonProps;
    /**
     * props for the subsidiary ActionButton, which is close to the primary button
     */
    subsidiaryBtnProps?: IButtonProps;
    /**
     * Ref for content element
     */
    contentRef?: MutableRefObject<HTMLDivElement | null>;
    closeButtonAriaLabel?: string;
    onDismiss?: () => void;
    titleId?: string;
    subTitleId?: string;
    contentClassName?: string;
    headerClassName?: string;
    footerClassName?: string;
    isFetching?: boolean;
    titleFarItem?: React.ReactNode;
    footerItem?: React.ReactNode;
    contentHidden?: boolean;
    shareSubmissionCardConsentShow?: boolean;
    shareSubmissionCardConsentOptionChecked?: boolean;
    onShareSubmissionCardConsentOptionChange?: (isChecked: boolean) => void;
    shareSubmissionCardConsentOptionLabel?: string;
    placeHolderItem?: React.ReactNode;
}>;
export const ModalContent: React.FC<ModalContentProps> = ({
    headerProps,
    secondaryBtnProps,
    subsidiaryBtnProps,
    primaryBtnProps,
    children,
    contentRef,
    closeButtonAriaLabel,
    onDismiss,
    titleId,
    subTitleId,
    contentClassName,
    headerClassName,
    footerClassName,
    isFetching,
    titleFarItem,
    footerItem,
    shareSubmissionCardConsentShow,
    shareSubmissionCardConsentOptionChecked,
    onShareSubmissionCardConsentOptionChange,
    shareSubmissionCardConsentOptionLabel,
    placeHolderItem,
}) => {
    const styles = useContentStyles();
    const shareSubmissionCardConsentStyles = useShareSubmissionCardConsentStyles();

    return (
        <>
            <div role='alert' aria-busy={!headerProps} className={mergeStyles(styles.header, headerClassName)}>
                <CommonIcon titleAs='h1' titleId={titleId} descriptionId={subTitleId} {...headerProps} />
                {titleFarItem}
                {onDismiss && (
                    <IconButton
                        iconProps={{ iconName: 'Cancel' }}
                        ariaLabel={closeButtonAriaLabel}
                        title={closeButtonAriaLabel}
                        onClick={onDismiss}
                    />
                )}
            </div>
            <CommonLoadingIndicator isFetching={isFetching} />
            <div className={mergeStyles(styles.content, contentClassName)} ref={contentRef} data-is-scrollable>
                {placeHolderItem && <div className={styles.placeHolder}>{placeHolderItem}</div>}
                {children}
            </div>
            <div className={mergeStyles(styles.footer, footerClassName)}>
                {shareSubmissionCardConsentShow && (
                    <Checkbox
                        label={shareSubmissionCardConsentOptionLabel}
                        checked={shareSubmissionCardConsentOptionChecked}
                        onChange={(_, checked) => onShareSubmissionCardConsentOptionChange?.(checked!)}
                        styles={shareSubmissionCardConsentStyles}
                    />
                )}
                {secondaryBtnProps && <ActionButton {...secondaryBtnProps} />}
                {footerItem}
                {subsidiaryBtnProps && <DefaultButton {...subsidiaryBtnProps} />}
                {primaryBtnProps && <PrimaryButton primary {...primaryBtnProps} />}
            </div>
        </>
    );
};
