import { FontSizes, isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const usePeopleSelectStyles = makeStyles(theme => ({
    label: {
        fontSize: FontSizes.size14,
        padding: '0 0 8px',
    },
    errorMessage: {
        color: theme.palette.redDark,
        padding: '4px 12px',
        fontSize: FontSizes.size12,
    },
    hint: {
        fontSize: FontSizes.size12,
        color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
    },
    picker: {
        '.ms-TooltipHost': {
            display: 'block',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
        },
        '.ms-BasePicker-text': {
            backgroundColor: theme.palette.neutralLight,
            '.ms-PickerPersona-container': {
                margin: '4px',
                backgroundColor: theme.palette.white,
            },
            '.ms-BasePicker-input::placeholder': {
                color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
            },
        },
        '.ms-PickerPersona-container:hover .ms-PickerItem-removeButton .ms-Button-icon': {
            color: isContrastTheme(theme.name) ? theme.palette.white : undefined,
            backgroundColor: isContrastTheme(theme.name) ? theme.palette.themeSecondary : undefined,
        },
        '.ms-PickerItem-removeButton .ms-Button-icon': {
            color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,
        },
    },
    unremovable: {
        '.ms-PickerItem-removeButton': {
            display: 'none',
        },
    },
    moreButtonMenu: {
        '.ms-Persona-details': {
            flexDirection: 'row',
        },
        '.ms-Persona-secondaryText': {
            display: 'flex',
            marginRight: '-10px',
        },
        '.ms-Button--hasMenu': {
            height: '20px',
        },
    },
    expandableCoinOverlay: {
        position: 'absolute',
        visibility: 'hidden',
        '.ms-Persona:hover &, .ms-Persona:focus-visible &': {
            visibility: 'visible',
        },
    },
}));
