import { Semaphore } from '@addressbook/utilities-core';
import { ServiceInterceptor } from '../types';

export interface ThrottlingInterceptorOptions {
    maxConcurrency?: number;
}

const defaultMaxConcurrency = 8;

/**
 * Create a service interceptor to throttle requests.
 *
 * @property maxConcurrency The maximum number of concurrent requests.
 * @returns An interceptor.
 */
export function createThrottlingInterceptor({
    maxConcurrency = defaultMaxConcurrency,
}: ThrottlingInterceptorOptions = {}): ServiceInterceptor {
    const semaphore = new Semaphore(maxConcurrency);
    return (request, proceed) => semaphore.run(() => proceed(request));
}
