    OpenDialog = 'openDialog',
    OpenPopup = 'openPopup',
    Read = 'read',
    RemoveBanner = 'removeBanner',
    RemoveChiclet = 'removeChiclet',
    Retry = 'retry',
    Select = 'select',
    Show = 'show',
    ShowBanner = 'showBanner',
    Submit = 'submit',
    Toggle = 'toggle',
    Update = 'update',
