import React from 'react';
import { Stack } from '@fluentui/react';
import { CommonIcon } from '../CommonIcon';
import { useAppHeaderStyles } from './AppHeader.styles';

export interface AppHeaderProps {
    icon?: string | JSX.Element;
    accentColor?: string;
    title?: string;
    items?: JSX.Element;
    farItems?: JSX.Element;
    moduleName?: string;
}

export const AppHeader: React.FC<AppHeaderProps> = ({ items, farItems, icon, accentColor, title }) => {
    const headerStyles = useAppHeaderStyles();
    return (
        <Stack as='header' horizontal disableShrink horizontalAlign='space-between' className={headerStyles.root}>
            <CommonIcon
                icon={icon}
                iconBackgroundColor={accentColor}
                title={title}
                titleAs='h1'
                styles={{ title: headerStyles.text }}
            />
            <Stack verticalAlign='center' grow>
                {items}
            </Stack>
            <Stack horizontalAlign='center' verticalAlign='center'>
                {farItems}
            </Stack>
        </Stack>
    );
};
