import { IModalStyles, ITextFieldStyles } from '@fluentui/react';
import { makeStyles } from '../styles/makeStyles';

export const useCopyResponseModalStyles = /* #__PURE__*/ makeStyles<Partial<IModalStyles>>({
    main: {
        width: '25rem',
        height: '18rem',
        maxHeight: '100%',
    },
    scrollableContent: {
        height: '100%',
        width: '100%',
        overflow: 'hidden',
        display: 'flex',
        flexDirection: 'column',
        padding: '1.1875rem 2rem 2.5rem 2rem',
        boxSizing: 'border-box',
    },
});

export const useContentStyles = /* #__PURE__*/ makeStyles(theme => ({
    header: {
        width: '100%',
        maxWidth: '100%',
        height: '1rem',
        display: 'flex',
        alignItems: 'center',
        '>.ms-Button--icon': {
            width: '1rem',
            height: '1rem',
        },
    },
    contents: {
        width: '100%',
        maxWidth: '100%',
        display: 'flex',
        alignItems: 'center',
        marginTop: '1rem',
    },
    copySuccessIcon: {
        width: '2.75rem',
        height: '2.75rem',
        display: 'flex',
        justifyContent: 'center',
    },
    copyTitle: {
        width: '100%',
        maxWidth: '100%',
        marginTop: '1rem !important',
        span: {
            color: theme.palette.black,
        },
    },
    copyContent: {
        width: '100%',
        maxWidth: '100%',
        flex: '0 0 auto',
        marginTop: '1.25rem !important',
    },
    linkPermissionSetting: {
        width: '21rem',
        height: '3rem',
        display: 'flex',
        alignItems: 'center',
        border: `1px solid ${theme.palette.neutralLight}`,
        borderRadius: '3px',
        marginTop: '1.25rem !important',
    },
    linkPermissionSettingIcon: {
        display: 'flex',
        justifyContent: 'center',
        width: '2rem',
        height: '2rem',
        margin: '0 0.5rem',
    },
    linkPermissionSettingTextStyles: [
        {
            width: '15rem',
            maxWidth: '15rem',
        },
    ],
}));

export function useLinkPlaceTextFieldStyles(): Partial<ITextFieldStyles> {
    return {
        fieldGroup: {
            width: '14.75rem',
            height: '1.875rem !important',
            '.ms-TextField-fieldGroup:hover': {
                borderWidth: '0 0 0 2px',
            },
            '.ms-TextField-fieldGroup:focus': {
                borderWidth: '0 0 0 2px',
            },
        },
    };
}
