import { mergeStyles } from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';
import React from 'react';
import { PeopleCoin } from '../PeopleCoin';
import { FormatOptionalStringBySelection, PeopleSelection, RetrieveIcon } from '../types';
import { usePeopleListStyles } from './PeopleList.styles';

/**
 * The props for the PeopleList component, which renders a list of people in a vertical layout.
 *
 * @property label The label for the list.
 * @property selections The people to display.
 * @property formatName A function to format the name for each person.
 * @property formatDescription A function to format the description for each person.
 * @property onRetrieveIcon A function to retrieve the icon for each person.
 * @property onItemClick A callback for when a person is clicked.
 * @property labelClassName The class name for the label.
 * @property listClassName The class name for the list.
 * @property itemClassName The class name for each person item.
 */
export interface PeopleListProps extends React.HTMLAttributes<HTMLDivElement> {
    label?: string;
    selections: PeopleSelection[];
    formatName?: FormatOptionalStringBySelection;
    formatDescription?: FormatOptionalStringBySelection;
    onRetrieveIcon?: RetrieveIcon;
    onItemClick?: (selection: PeopleSelection) => void;
    labelClassName?: string;
    listClassName?: string;
    itemClassName?: string;
}

export const PeopleList: React.FC<PeopleListProps> = ({
    label,
    selections,
    formatName,
    formatDescription,
    onRetrieveIcon,
    onItemClick,
    labelClassName,
    listClassName,
    itemClassName,
    ...props
}) => {
    const labelId = useId('label');

    const styles = usePeopleListStyles();
    return (
        <div {...props}>
            {label && (
                <div id={labelId} className={mergeStyles(styles.label, labelClassName)}>
                    {label}
                </div>
            )}
            <div className={mergeStyles(styles.list, listClassName)} role='list' aria-labelledby={labelId}>
                {selections.map(selection => (
                    <div
                        key={selection.id}
                        className={mergeStyles(styles.item, itemClassName)}
                        role='listitem'
                        onClick={() => onItemClick?.(selection)}>
                        <PeopleCoin selection={selection} onRetrieveIcon={onRetrieveIcon} coinSize={24} />
                        <div className={styles.text}>
                            <div>{formatName?.(selection) ?? selection.name}</div>
                            <div>{formatDescription?.(selection)}</div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};
