import type { ICheckboxStyles, IModalStyles } from '@fluentui/react';
import { makeStyles } from '../styles/makeStyles';
import { ellipsisStyles } from '../styles/ellipsisStyles';

const horizontalPaddingGap = '30px';
const verticalPaddingGap = '24px';
export const useModalStyles = /* #__PURE__*/ makeStyles<Partial<IModalStyles>>({
    main: {
        width: '1000px',
        height: '700px',
        maxHeight: '100%',
        maxWidth: '100%',
    },
    scrollableContent: {
        height: '100%',
        width: '100%',
        overflow: 'hidden',
        display: 'flex',
        flexDirection: 'column',
        paddingTop: '30px',
        paddingBottom: '20px',
        gap: '10px',
        boxSizing: 'border-box',
    },
});

export const useContentStyles = /* #__PURE__*/ makeStyles({
    placeHolder: {
        paddingBottom: '0.5rem',
    },
    header: {
        display: 'flex',
        height: '2.25rem',
        alignItems: 'center',
        flexShrink: 0,
        padding: `0 ${horizontalPaddingGap}`,
        '>:first-child': {
            flex: '1 1 auto',
            minWidth: 0,
        },
    },
    content: {
        flex: '1 1 auto',
        overflowY: 'auto', // fallback for FireFox
        'overflow-y': 'overlay', // make scrollbar float over content
        padding: `${verticalPaddingGap} ${horizontalPaddingGap}`, // using the same inner padding to make scrollbar right
    },
    footer: {
        padding: `0 ${horizontalPaddingGap}`,
        display: 'flex',
        justifyContent: 'flex-end',
        alignItems: 'center',
        gap: '0.5rem',
        '>.ms-Button--action': {
            marginInlineEnd: 'auto',
        },
    },
});

export const useShareSubmissionCardConsentStyles = makeStyles<Partial<ICheckboxStyles>>(theme => ({
    root: {
        '&.ms-Checkbox': {
            padding: 0,
        },
    },
    checkbox: {
        borderColor: theme.palette.neutralDark,
    },
    text: [
        {
            width: '10rem',
        },
        ellipsisStyles(1),
    ],
}));
