import { FontWeights } from '@fluentui/react';
import { isContrastTheme, rem } from '@addressbook/components-fluentui-teams-theme';
import { ellipsisStyles } from '../styles/ellipsisStyles';
import { makeStyles } from '../styles/makeStyles';
import { CommonIconSize } from './CommonIcon.component';

export const useCommonIconStyles = makeStyles(theme => ({
    root: { display: 'flex', justifyContent: 'space-between' },
    content: {
        display: 'flex',
        columnGap: '12px',
        alignItems: 'center',
        overflow: 'hidden',
    },
    iconContainer: {
        position: 'relative',
        height: '2rem',
        width: '2rem',
        display: 'flex',
        alignContent: 'center',
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: '4px',
        flexShrink: '0 !important',
        '&:hover .edit-icon': {
            visibility: 'visible',
        },
        [`.${CommonIconSize.Size28} &`]: { height: '28px', width: '28px' },
        [`.${CommonIconSize.Size32} &`]: { height: '32px', width: '32px' },
        [`.${CommonIconSize.Size40} &`]: { height: '40px', width: '40px' },
        [`.${CommonIconSize.Size44} &`]: { height: '44px', width: '44px' },
        [`.${CommonIconSize.Size48} &`]: { height: '48px', width: '48px' },
        [`.${CommonIconSize.Size64} &`]: { height: '64px', width: '64px' },
        [`.${CommonIconSize.Size80} &`]: { height: '80px', width: '80px' },
    },
    icon: {
        color: 'white',
        fontSize: '1rem',

        [`.${CommonIconSize.Size28} &`]: theme.fonts.mediumPlus,
        [`.${CommonIconSize.Size32} &`]: theme.fonts.xLarge,
        [`.${CommonIconSize.Size40} &`]: theme.fonts.xLargePlus,
        [`.${CommonIconSize.Size44} &`]: theme.fonts.xxLarge,
        [`.${CommonIconSize.Size48} &`]: theme.fonts.xxLargePlus,
        [`.${CommonIconSize.Size64} &`]: { fontSize: rem(48) },
        [`.${CommonIconSize.Size80} &`]: { fontSize: rem(48) },
    },
    editIcon: [
        'edit-icon',
        {
            cursor: 'pointer',
            visibility: 'hidden',
            color: theme.palette.white,
            position: 'absolute',
            fontSize: '8px',
            background: theme.palette.neutralDark,
            padding: '6px',
            borderRadius: '50%',
            bottom: '4px',
            right: '4px',
        },
    ],
    textContainer: { overflow: 'hidden' },
    titleContainer: {
        display: 'flex',
        flexDirection: 'row',
    },
    title: [
        ellipsisStyles(1),
        {
            fontWeight: FontWeights.semibold,
            color: isContrastTheme(theme.name) ? 'inherit' : theme.palette.black,
            margin: 0,
        },
    ],
    description: [
        theme.fonts.small,
        ellipsisStyles(1),
        {
            color: isContrastTheme(theme.name) ? 'inherit' : theme.palette.neutralDark,
        },
    ],
}));
