import {
    DirectionalHint,
    IPeoplePickerItemSelectedProps,
    IPeoplePickerProps,
    IPersonaProps,
    IPickerItemProps,
    Label,
    mergeStyles,
    NormalPeoplePicker,
    PeoplePickerItem,
    ValidationState,
} from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';
import React from 'react';
import { PeopleCoin } from '../PeopleCoin';
import { PeopleSelection, PeopleSelectionType, WithSelection } from '../types';
import { peopleSelectDefaultStrings, PeopleSelectStrings } from './PeopleSelect.strings';
import { usePeopleSelectStyles } from './PeopleSelect.styles';
import { PeopleSelectProps } from './PeopleSelect.types';
import {
    extractSelectionFromPersonaProps,
    filterSuggestions,
    normalizeFilter,
    resolvePeopleSelectionItems,
    tryExpandPeopleSelection,
    useMapPeopleSelectionToPersonaProps,
} from './PeopleSelect.utils';

/**
 * A component for selecting people.
 *
 * The component offers:
 * - Unified props based on PeopleSelection interface for both single and many selection.
 * - Expandable ManyPeopleSelection with factory and promise support.
 * - Validation support using validateSelections prop.
 * - Filter text normalization.
 * - Suggestions filtering based on selections.
 * - Prevent removal of unremovable items.
 * - Localized strings.
 */
export const PeopleSelect: React.FC<PeopleSelectProps> = ({
    label,
    hint,
    required,
    selections,
    errorMessage,
    suggestionsErrorMessage,
    onSelectionsChange,
    onErrorMessageChange,
    onResolveSuggestions,
    allowEmptySuggestions,
    onRenderItem,
    onExpandPeopleSelection,
    onRenderMoreButtonMenu,
    onRetrieveIcon,
    describeSuggestion,
    validateSelections,
    strings,
    formatName,
    pickerClassName,
    resolveDelay,
    disabled,
    itemLimit,
    ...props
}) => {
    const stringsInternal = React.useMemo<PeopleSelectStrings>(
        () => ({ ...peopleSelectDefaultStrings, ...strings }),
        [strings]
    );

    const mapPeopleSelectionToPersonaProps = useMapPeopleSelectionToPersonaProps({
        onRetrieveIcon,
        describeSuggestion,
    });

    const selectionsRef = React.useRef(selections);
    selectionsRef.current = selections;

    React.useEffect(() => {
        selectionsRef.current.map(resolvePeopleSelectionItems);
    }, []);

    const selectedItems = React.useMemo<WithSelection<IPersonaProps>[]>(
        () => selections.map(mapPeopleSelectionToPersonaProps),
        [mapPeopleSelectionToPersonaProps, selections]
    );

    const triggerChange = React.useCallback(
        (selections: PeopleSelection[]) => {
            const errorMessage =
                required && !selections.length
                    ? stringsInternal.requiredErrorMessage
                    : validateSelections?.(selections);

            onSelectionsChange?.(selections);
            onErrorMessageChange?.(errorMessage);
        },
        [onErrorMessageChange, onSelectionsChange, required, stringsInternal.requiredErrorMessage, validateSelections]
    );

    const triggerChangeRef = React.useRef(triggerChange);
    triggerChangeRef.current = triggerChange;

    const onChange = React.useCallback<NonNullable<IPeoplePickerProps['onChange']>>((items = []) => {
        const newSelections = items.map(item => extractSelectionFromPersonaProps(item));

        // Prevent removal of unremovable items
        const unremovableSelections = selectionsRef.current.filter(selection => selection.unremovable);
        if (!unremovableSelections.every(selection => newSelections.includes(selection))) return;

        triggerChangeRef.current(newSelections);
    }, []);

    const triggerResolveSuggestions = React.useCallback(
        async (filter: string) => {
            const normalizedFilter = normalizeFilter(filter);
            const suggestions = await onResolveSuggestions(normalizedFilter);
            const filteredSuggestions = filterSuggestions(suggestions, selectionsRef.current);
            return filteredSuggestions.map(mapPeopleSelectionToPersonaProps);
        },
        [mapPeopleSelectionToPersonaProps, onResolveSuggestions]
    );

    const onResolveSuggestionsInternal = React.useCallback<IPeoplePickerProps['onResolveSuggestions']>(
        filter => triggerResolveSuggestions(filter),
        [triggerResolveSuggestions]
    );

    const onEmptyResolveSuggestionsInternal = React.useCallback<
        NonNullable<IPeoplePickerProps['onEmptyResolveSuggestions']>
    >(() => triggerResolveSuggestions(''), [triggerResolveSuggestions]);

    const onItemSelected = React.useCallback<NonNullable<IPeoplePickerProps['onItemSelected']>>(selectedItem => {
        if (!selectedItem) return null;

        // Try to resolve the selection's items
        const selection = extractSelectionFromPersonaProps(selectedItem);
        resolvePeopleSelectionItems(selection);

        return selectedItem;
    }, []);

    const onExpandPeopleSelectionRef = React.useRef(onExpandPeopleSelection);
    onExpandPeopleSelectionRef.current = onExpandPeopleSelection;

    const styles = usePeopleSelectStyles();
    const onRenderItemInternal = React.useCallback<NonNullable<IPeoplePickerProps['onRenderItem']>>(
        props => {
            const selection = extractSelectionFromPersonaProps(props.item);
            const moreButtonMenu = onRenderMoreButtonMenu?.(selection);
            const newProps: IPeoplePickerItemSelectedProps & IPickerItemProps<IPersonaProps> = {
                ...props,
                item: {
                    ...props.item,
                    ValidationState: ValidationState.valid,
                    onRenderPersonaCoin: props => (
                        <>
                            <PeopleCoin
                                {...props}
                                selection={selection}
                                onRetrieveIcon={onRetrieveIcon}
                                coinSize={24}
                            />
                            {selection.type !== PeopleSelectionType.User && selection.expandable && (
                                <PeopleCoin
                                    {...props}
                                    selection={{ ...selection, icon: 'AddFilled' }}
                                    onRetrieveIcon={onRetrieveIcon}
                                    coinSize={24}
                                    className={styles.expandableCoinOverlay}
                                />
                            )}
                        </>
                    ),
                    onClick: () => {
                        // Try to expand the selection
                        const newSelections = tryExpandPeopleSelection(selectionsRef.current, selection);
                        if (!newSelections) return;
                        onExpandPeopleSelectionRef.current?.(selection);
                        triggerChangeRef.current(newSelections);
                    },
                    text: formatName?.(selection) ?? selection.name,
                    onRenderSecondaryText: moreButtonMenu ? () => moreButtonMenu : undefined,
                },
                className: mergeStyles(
                    props.className,
                    selection.unremovable && styles.unremovable,
                    moreButtonMenu && styles.moreButtonMenu
                ),
            };

            const item = <PeoplePickerItem {...newProps} />;
            return onRenderItem?.(selection, item) ?? item;
        },
        [
            onRenderMoreButtonMenu,
            formatName,
            styles.unremovable,
            styles.moreButtonMenu,
            styles.expandableCoinOverlay,
            onRenderItem,
            onRetrieveIcon,
        ]
    );

    const labelId = useId('label');
    const hintId = useId('hint');
    const errorMessageId = useId('errorMessage');

    return (
        <div {...props}>
            <Label id={labelId} className={styles.label} required={required}>
                {label}
            </Label>
            <NormalPeoplePicker
                className={mergeStyles(styles.picker, pickerClassName)}
                selectedItems={selectedItems}
                onChange={onChange}
                onResolveSuggestions={onResolveSuggestionsInternal}
                // This is intentionally disabled to avoid the suggestions taking the screen reader focus immediately
                // after focus is moved to the input field. It used to cause the screen reader miss the picker label,
                // placeholder, and other important information.
                onEmptyResolveSuggestions={allowEmptySuggestions ? onEmptyResolveSuggestionsInternal : undefined}
                onItemSelected={onItemSelected}
                onRenderItem={onRenderItemInternal}
                inputProps={{
                    'aria-labelledby': labelId,
                    ...(hint && { 'aria-describedby': hintId }),
                    ...(selections.length === 0 && { placeholder: stringsInternal.inputPlaceholder }),
                    ...(required && { 'aria-required': true, required: true }),
                    ...(errorMessage && { 'aria-invalid': true, 'aria-errormessage': errorMessageId }),
                }}
                pickerCalloutProps={{
                    directionalHint: DirectionalHint.bottomLeftEdge,
                    directionalHintForRTL: DirectionalHint.bottomRightEdge,
                    alignTargetEdge: true,
                    styles: { root: { '.ms-Persona': { width: '236px' } } },
                }}
                pickerSuggestionsProps={{
                    suggestionsHeaderText: stringsInternal.suggestionsHeaderText,
                    noResultsFoundText: suggestionsErrorMessage || stringsInternal.noResultsFoundText,
                    suggestionsAvailableAlertText: stringsInternal.suggestionsAvailableAlertText,
                }}
                removeButtonAriaLabel={stringsInternal.removeButtonAriaLabel}
                resolveDelay={resolveDelay}
                disabled={disabled}
                itemLimit={itemLimit}
            />
            {hint && (
                <div id={hintId} className={styles.hint}>
                    {hint}
                </div>
            )}
            {errorMessage && (
                <div id={errorMessageId} className={styles.errorMessage} role='alert'>
                    {errorMessage}
                </div>
            )}
        </div>
    );
};
