import { useRef, useMemo } from 'react';

type Update<T> = (updater: (value: T) => T) => void;

/**
 * A hook to create an update function upon an onChange callback.
 *
 * This is useful if your onChange callback has a setState (returned by useState) sink, but you want
 * to call it in the same render cycle. The update function returned by this hook will retain the
 * latest value (instead of having to wait for the passed in state update) and call the onChange
 * callback immediately.
 *
 * @param value The value to use.
 * @param onChange A function to call when the value changes.
 * @returns A proxy function that will call the onChange callback with the updated value.
 *
 * @example
 * const [value, setValue] = useState(0);
 * const onChange: (value: number) => void = setValue;
 *
 * const addValueByTwo = () => onChange(value + 2); // works
 * const addValueByTwo = () => { onChange(value + 1); onChange(value + 1); }; // doesn't work, value will be 1
 *
 * const updateValue = useUpdate(value, onChange);
 * const addValueByTwo = () => updateValue(value => value + 2); // works
 * const addValueByTwo = () => { updateValue(value => value + 1); updateValue(value => value + 1); }; // works
 */
export function useUpdate<T>(value: T, onChange: undefined): undefined;
export function useUpdate<T>(value: T, onChange: (value: T) => void): Update<T>;
export function useUpdate<T>(value: T, onChange?: (value: T) => void): Update<T> | undefined;
export function useUpdate<T>(value: T, onChange?: (value: T) => void): Update<T> | undefined {
    const valueRef = useRef(value);
    valueRef.current = value;

    return useMemo(() => {
        if (!onChange) return;

        return updater => {
            const newValue = updater(valueRef.current);
            valueRef.current = newValue;
            onChange(newValue);
        };
    }, [onChange]);
}
