import { delay } from '@addressbook/utilities-core';
import { ServiceInterceptor } from '../types';
import { isServerErrorOrTooManyRequests, TestResponse } from './utilities';

export type Backoff = (retries: number, response?: Response) => number;

export interface RetryInterceptorOptions {
    test?: TestResponse;
    maxRetries?: number;
    backoff?: Backoff;
}

const defaultMaxRetries = 3;

/**
 * Create a service interceptor that retries requests.
 *
 * @property test A function to test if the response should be retried.
 * @property maxRetries The maximum number of retries.
 * @property backoff A function to calculate the delay between retries, in milliseconds.
 * @returns An interceptor.
 */
export function createRetryInterceptor({
    test = isServerErrorOrTooManyRequests,
    maxRetries = defaultMaxRetries,
    backoff,
}: RetryInterceptorOptions = {}): ServiceInterceptor {
    return async (request, proceed) => {
        let retries = 0;
        while (true) {
            try {
                const response = await proceed(request.clone());
                if (test(response) && retries++ < maxRetries) {
                    if (backoff) await delay(backoff(retries, response));
                    continue;
                }
                return response;
            } catch (error) {
                if (retries++ < maxRetries) {
                    if (backoff) await delay(backoff(retries));
                    continue;
                }
                throw error;
            }
        }
    };
}
