import { AWTEventData, AWTEventProperties, AWTPropertyType } from '@aria/webjs-sdk';
import { PanelRegion, PanelType, ActionOutcome, PanelLaunchMethod, CommonLogCategory } from './types';

/**
 * Details bag for logs
 *
 * @param message message
 * @param error error
 * @param details Details (callstack, source, stringified details)
 * @param method method
 */
export interface ITelemetryDetails {
    message?: string;
    error?: Error;
    details?: string;
    method?: string;
}

export interface IAWTLogger {
    logEvent: (event: AWTEventProperties | AWTEventData | string) => void;
    setContext: (name: string, value: string | number | boolean, type?: AWTPropertyType) => void;
}

export interface ILogger<TScenarioName extends string, TStepName extends string, TLogCategory extends string> {
    /**
     * session id
     */
    readonly sessionId: string;

    /**
     * Used to start log event at once even in delay time
     */
    activeLogger: () => void;

    /**
     * Set Compliant Aria Client for EUDB
     */
    setCompliantAriaClient: (tenantId: string) => Promise<void>;

    /**
     * Used to add/update common property for logger
     *
     * @param dataMap dictionary of common properties to update/add
     */
    setCommonProperty: (dataMap: Record<string, string | number | boolean | undefined>) => void;

    /**
     * Used to log scenario relative data
     *
     * @param name Scenario name
     * @param eventData Data of the event
     * @param completeScenario if the scenario should be stopped
     */
    logScenario: (name: string, eventData: IScenarioEventData, completeScenario: boolean) => void;

    /**
     * Used to log a performance signal
     *
     * @param action Action
     * @param source Source
     * @param duration Time measurement in MS
     * @param duartionDetails Other duration details as needed
     * @param dataBag databag
     */
    logPerformance: (
        action: string,
        source: string,
        duration: number,
        duartionDetails?: { [key: string]: number },
        dataBag?: any
    ) => void;

    /**
     * Used to log a http request and response
     *
     * @param httpInfo data bag containing http info
     */
    logHttp: (httpInfo: IHttpInfo) => void;

    /**
     * Used to log a UI Event
     *
     * @param panelLaunchMethod how the panel was launched
     * @param panelRegion The region of the page the panel is lcoated in
     * @param panelType indicates which panel the action was performed in
     * @param dataBag Other details as needed
     */
    logUserBiView: (
        panelLaunchMethod: PanelLaunchMethod,
        panelRegion: PanelRegion,
        panelType: PanelType,
        dataBag?: any
    ) => void;

    /**
     * Used to log a user action
     *
     * @param scenarioName The action category
     * @param scenarioType The action scenario
     * @param moduleName name of the module clicked (name of the class)
     * @param dataBag Other details as needed
     * @param pageLeave Indicate whether the current action is leaving current page, if yes, it will raise additional event to track the event with stay time
     */
    logUserBiAction: (
        scenarioName: TScenarioName,
        scenarioType: string,
        moduleName: string,
        dataBag?: any,
        pageLeave?: boolean
    ) => void;

    /**
     * Used to log an API Action
     *
     * @param nonUiComponent The non UI component
     * @param event The event
     * @param details Other details as needed
     */
    logNonUiTelemetry: (nonUiComponent: string, outcome: ActionOutcome, details?: any) => void;

    /**
     * Used to log debug informational messages - noisy chatter for debugging issues to the trace table
     *
     * @param logCategory The component/category for which this log is relevant to
     * @param message The message to log
     */
    logTraceDebug: (logCategory: TLogCategory | CommonLogCategory, message: string) => void;

    /**
     * Used to log informational messages to the trace table
     *
     * @param logCategory The component/category for which this log is relevant to
     * @param message The message to log
     */
    logTraceInfo: (logCategory: TLogCategory | CommonLogCategory, message: string) => void;

    /**
     * Used to log warning messages to the trace table
     *
     * @param logCategory The component/category for which this log is relevant to
     * @param message The message to log
     */
    logTraceWarning: (logCategory: TLogCategory | CommonLogCategory, message: string) => void;

    /**
     * Used to log error messages to the trace table
     *
     * @param logCategory The component/category for which this log is relevant to
     * @param message The message to log
     */
    logTraceError: (logCategory: TLogCategory | CommonLogCategory, message: string) => void;

    /**
     * Used to log an exception to the trace table when callstack is available
     *
     * @param logCategory logCategory
     * @param details Details (callstack, source, stringified details)
     * @param dataBag Any additional custom data bags to be emitted as ARIA columns. Use only for really specific cases,
     * for any generic use, add a structured new param to this method definition
     */
    logTraceException: (
        logCategory: TLogCategory | CommonLogCategory,
        details: ITelemetryDetails,
        dataBag?: any
    ) => void;

    /**
     * Used to log an HTTP exception to the trace table
     *
     * @param httpAPI The API name to be printed in the trace log for readability on which API failed
     * @param message Message
     * @param httpStatusCode HTTP Status code of the response
     */
    logTraceHttpException: (httpAPI: string, message: string, httpStatusCode: number) => void;

    /**
     * Creates a scenario logger
     *
     * @param name The name of the scenario
     * @param dataBag data info log when start scenario
     */
    createScenario(name: string, dataBag?: any): IScenarioLogger<TStepName>;

    /**
     * Finds an active scenario logger
     *
     * @param name The name of the scenario
     */
    findScenario(name: string): IScenarioLogger<TStepName> | null;

    /**
     * Finds an active scenario logger and mark it with step and data
     *
     * @param scenarioName The name of the scenario
     * @param step The name of the step
     * @param status The status
     * @param eventData Event data
     */
    markScenario(
        scenarioName: TScenarioName,
        step: TStepName,
        status?: TelemetryScenarioStatus,
        eventData?: Partial<IScenarioEventData>
    ): void;

    /**
     * End a scenario and delete it
     *
     * @param scenarioName The name of the scenario
     * @param eventData Event data
     */
    stopScenario(scenarioName: string, eventData?: Partial<IScenarioEventData>): void;

    /**
     * End a scenario with failure and delete it
     *
     * @param scenarioName The name of the scenario
     * @param eventData Event data
     */
    failScenario(scenarioName: string, eventData?: Partial<IScenarioEventData>): void;

    /**
     * Forcefully remove scenario, only to be used in cases with task module limitations.
     *
     * @param name  The name of the scenario
     */
    markScenarioComplete(name: string): void;

    /**
     * Function to call to flush logs
     *
     * @param callback the function that is called when flush finishes.
     */
    flushLogs(callback: () => void): void;

    /**
     * Prevents new events from being added for transmission. It also batches all
     * events currently in the queue, and creates requests for them to be sent. If
     * HTML5 Beacons are supported, then they will be used.
     * */
    flushAndTeardown(): void;
}

// interface for logging http info. Will change over time.
export interface IHttpInfo {
    apiName?: string;
    url: string;
    clientRequestId: string;
    correlationId: string;
    latency?: number;
    getTeamsContextLatency?: number;
    getAuthTokenLatency?: number;
    requestMethod: string;
    responseCode?: number;
    responseText?: string;
    edgeRef?: string;
}

export interface IScenarioLogger<TStepName extends string> {
    elapsedTime: number;
    getCurrentElapsedTime(): number;
    mark(stepName: TStepName, status?: TelemetryScenarioStatus, eventData?: Partial<IScenarioEventData>): void;
    stop(eventData?: Partial<IScenarioEventData>): void;
    fail(eventData?: Partial<IScenarioEventData>): void;
}

export enum ScenarioColumns {
    Delta = 'delta',
    Elapsed = 'elapsed',
    Error = 'error',
    ErrorCode = 'errorCode',
    ScenarioName = 'Scenario.Name',
    ScenarioStatus = 'Scenario.Status',
    ScenarioStep = 'Scenario.Step',
    StartReason = 'startReason',
    StatusCode = 'statusCode',
    StepDelta = 'stepDelta',
    StepElapsed = 'stepElapsed',
}

export enum TelemetryScenarioStatus {
    Abandoned = 'abandoned',
    Failure = 'failure',
    Incomplete = 'incomplete',
    Init = 'init',
    Success = 'success',
    Timeout = 'timeout',
}

export enum TelemetryScenarioStep {
    Finish = 'finish',
    Pause = 'pause',
    Start = 'start',
    Test = 'test',
}

export interface ScenarioProperties {
    /**
     * Name of the scenario marker
     */
    [ScenarioColumns.ScenarioName]?: string;
    [ScenarioColumns.ScenarioStep]?: TelemetryScenarioStep | string;
    /**
     * Indicates the status of the scenario like 'success', 'failure', 'timeout', etc.
     */
    [ScenarioColumns.ScenarioStatus]?: TelemetryScenarioStatus;

    [ScenarioColumns.StartReason]?: string;
    [ScenarioColumns.StatusCode]?: string;

    [ScenarioColumns.ErrorCode]?: string;
    [ScenarioColumns.Error]?: Error;

    /**
     * Scenario Latency
     */
    [ScenarioColumns.Delta]?: number;
    /**
     * Allows to order the events during a given session. To be renamed as Scenario_Elapsed
     */
    [ScenarioColumns.Elapsed]?: number;

    /**
     * Step latency
     */
    [ScenarioColumns.StepDelta]?: number;
    [ScenarioColumns.StepElapsed]?: number;
}

export interface IScenarioEventData extends ScenarioProperties {
    id?: string;
    lastUpdatedTime?: Date;
    startTime?: Date;
    finishTime?: Date;
    dataBag?: any;
}
