import React from 'react';
import { IconButton, PrimaryButton, Stack, Text } from '@fluentui/react';
import { CommonIcon } from '../CommonIcon';
import { useContentStyles } from './ContentCopyRequestModal.styles';

export type ContentCopyRequestProps = React.PropsWithChildren<{
    copyRequestModalHeaderTitle: string;
    copyRequestModalCloseButton: string;
    copyRequestModalCopyButton: string;
    linkTargetTitle: string;
    linkPermissionSettingText: string;
    onDismiss?: () => void;
    LinkPermissionSettingIcon: JSX.Element;
    linkGenerateAndCopy?: () => void;
}>;
export const ContentCopyRequestModalContent: React.FC<ContentCopyRequestProps> = ({
    copyRequestModalHeaderTitle,
    copyRequestModalCloseButton,
    copyRequestModalCopyButton,
    linkTargetTitle,
    linkPermissionSettingText,
    onDismiss,
    LinkPermissionSettingIcon,
    linkGenerateAndCopy,
}) => {
    const styles = useContentStyles();

    return (
        <>
            <Stack horizontal horizontalAlign='space-between' role='alert' className={styles.header}>
                <CommonIcon
                    titleAs='h1'
                    className={styles.headerProperty}
                    title={copyRequestModalHeaderTitle}
                    description={linkTargetTitle}
                />
                {onDismiss && (
                    <IconButton
                        iconProps={{ iconName: 'Cancel' }}
                        ariaLabel={copyRequestModalCloseButton}
                        title={copyRequestModalCloseButton}
                        onClick={onDismiss}
                    />
                )}
            </Stack>
            <div className={styles.footer}>
                <Stack horizontal className={styles.linkPermissionSetting}>
                    <Stack horizontalAlign='center' className={styles.linkPermissionSettingIcon}>
                        {LinkPermissionSettingIcon}
                    </Stack>
                    <Text block className={styles.linkPermissionSettingTextStyles}>
                        {linkPermissionSettingText}
                    </Text>
                </Stack>
                <PrimaryButton primary text={copyRequestModalCopyButton} onClick={linkGenerateAndCopy} />
            </div>
        </>
    );
};
