import React from 'react';
import { mergeStyles, Shimmer, ShimmerElementType, Stack } from '@fluentui/react';
import { useCommonCardStyles } from './CommonCard.styles';

const LARGE_ICON_SIZE = 64;
const SMALL_ICON_SIZE = 32;

const LARGE_GAP_WIDTH = 18;
const SMALL_GAP_WIDTH = 12;

const LARGE_TEXT_HEIGHT = 64;
const SMALL_TEXT_HEIGHT = 20;

export const CommonCardShimmer = ({
    isMobile,
    isLowHeight,
    expanded,
}: {
    isMobile?: boolean;
    isLowHeight?: boolean;
    expanded?: boolean;
}): JSX.Element => {
    const cardStyles = useCommonCardStyles();
    const singleLine = (isMobile && isLowHeight) || expanded;

    const iconSize = expanded ? LARGE_ICON_SIZE : SMALL_ICON_SIZE;
    const gapWidth = expanded ? LARGE_GAP_WIDTH : SMALL_GAP_WIDTH;
    const textHeight = expanded ? LARGE_TEXT_HEIGHT : SMALL_TEXT_HEIGHT;

    return (
        <Stack
            className={mergeStyles(
                cardStyles.root,
                expanded && cardStyles.expandedRoot,
                isMobile && cardStyles.mobileRoot,
                isMobile && isLowHeight && cardStyles.mobileRootWithoutReminder
            )}
            verticalAlign='space-between'>
            <Shimmer
                width={singleLine ? undefined : iconSize}
                shimmerElements={[
                    { type: ShimmerElementType.line, height: iconSize, width: iconSize },
                    { type: ShimmerElementType.gap, width: gapWidth },
                    { type: ShimmerElementType.line, height: textHeight },
                ]}
            />
            {!singleLine && <Shimmer shimmerElements={[{ type: ShimmerElementType.line, height: textHeight }]} />}
        </Stack>
    );
};
