import React, { MutableRefObject } from 'react';
import { IPanelProps, IPanelStyles, IRenderFunction, Panel, PanelType } from '@fluentui/react';
import { usePanelStyles } from './CommonPanel.styles';

export type PanelContentProps = React.PropsWithChildren<{
    /**
     * props for render navigation content
     */
    onRenderNavigationContent?: IRenderFunction<IPanelProps>;
    /**
     * Ref for content element
     */
    contentRef?: MutableRefObject<HTMLDivElement | null>;
    onDismiss?: () => void;
    linkCopiedInfoItem?: JSX.Element;
}>;

export type CommonPanelProps = PanelContentProps & {
    isOpen: boolean;
};

export const CommonPanel: React.FC<CommonPanelProps> = ({
    isOpen,
    onRenderNavigationContent,
    onDismiss,
    contentRef,
    children,
    linkCopiedInfoItem,
}: CommonPanelProps): JSX.Element => {
    const panelStyles = usePanelStyles();

    return (
        <Panel
            styles={panelStyles as any as IPanelStyles}
            onRenderNavigationContent={onRenderNavigationContent}
            isOpen={isOpen}
            onDismiss={onDismiss}
            onOuterClick={(ev?: React.MouseEvent<HTMLDivElement>): void => {
                ev?.preventDefault();
            }}
            type={PanelType.custom}
            allowTouchBodyScroll
            hasCloseButton={!onRenderNavigationContent}>
            <div ref={contentRef} className={panelStyles.contentInner}>
                {children}
            </div>
            {linkCopiedInfoItem}
        </Panel>
    );
};
