import { menus } from '@microsoft/teams-js';
import { useEffect } from 'react';
import { noop } from '../utils';
import { setNavBarMenu } from '../wrappers';

export interface TeamsNavBarMenuItem {
    id: string;
    title?: string;
    icon?: string;
    disabled?: boolean;
    selected?: boolean;
    onClick?: () => void;
}

export function useTeamsNavBarMenu(items: TeamsNavBarMenuItem[]): void {
    useEffect(() => update(items), [items]);
    useEffect(() => () => update([]), []);
}

function update(items: TeamsNavBarMenuItem[]): void {
    setNavBarMenu(items.map(mapTeamsNavBarMenuItem), id => dispatchTeamsNavBarMenuItemOnClick(id, items)).catch(noop);
}

function mapTeamsNavBarMenuItem(item: TeamsNavBarMenuItem): menus.MenuItem {
    return {
        id: item.id,
        title: item.title ?? '',
        icon: item.icon ?? '',
        enabled: !item.disabled,
        selected: item.selected ?? false,
    };
}

function dispatchTeamsNavBarMenuItemOnClick(id: string, menuItems: TeamsNavBarMenuItem[]): boolean {
    const item = menuItems.find(item => item.id === id);

    if (!item || !item.onClick) return false;

    item.onClick();
    return true;
}
