import React from 'react';
import { Modal, mergeStyleSets, IModalStyles } from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';
import { useModalStyles } from './CommonModal.styles';
import { ModalContent, ModalContentProps } from './ModalContent';

export type CommonModalProps = ModalContentProps & {
    isOpen: boolean;
    modalStyles?: Partial<IModalStyles>;
};

export const CommonModal: React.FC<CommonModalProps> = ({
    isOpen,
    modalStyles,
    ...props
}: CommonModalProps): JSX.Element => {
    const styles = useModalStyles();
    const titleId = useId('ModalTitle');
    const subTitleId = useId('ModalSubTitle');
    return (
        <Modal
            titleAriaId={titleId}
            subtitleAriaId={subTitleId}
            styles={mergeStyleSets(styles, modalStyles)}
            isOpen={isOpen}
            isBlocking
            layerProps={{ eventBubblingEnabled: true }}
            focusTrapZoneProps={{ isClickableOutsideFocusTrap: true }}
            onDismiss={props.onDismiss}>
            <ModalContent titleId={titleId} subTitleId={subTitleId} {...props} />
        </Modal>
    );
};
