import React, { useMemo } from 'react';
import { mergeStyles } from '@fluentui/react';
import { rem } from '@addressbook/components-fluentui-teams-theme';
import { styles } from './EmptyState.styles';
import defultImg from './turtle.svg';

export interface IEmptyStateProps {
    title: string;
    image?: string | JSX.Element;
    message?: string;
    actionText?: string;
    className?: string;
    onAction?: () => void;
    background?: string;
    color?: string;
    btnColor?: string;
    height?: string;
    btnBackground?: string;
    role?: React.AriaRole;
    titleFontSize?: number | string;
}
export function EmptyState({
    title,
    message,
    image = defultImg,
    onAction,
    actionText,
    children,
    className,
    height,
    color = '#000',
    btnColor = '#242424',
    background = '#F5F5F5',
    btnBackground = '#FFF',
    role,
    titleFontSize = 18,
}: React.PropsWithChildren<IEmptyStateProps>): React.ReactElement {
    const titleId = useMemo(() => `dialogTitle${Math.random()}`.replace('.', '-'), []);
    const descId = useMemo(() => `dialogDesc${Math.random()}`.replace('.', '-'), []);

    return (
        <div className={mergeStyles(styles.root, { height, background, color }, className)}>
            {image && (
                <div aria-hidden>{typeof image === 'string' ? <img alt='' aria-hidden src={image} /> : image}</div>
            )}
            <div
                role={role}
                aria-atomic
                aria-labelledby={title && titleId}
                aria-describedby={message && descId}
                style={styles.info}>
                {title && (
                    <h2
                        id={titleId}
                        style={{ fontSize: typeof titleFontSize === 'number' ? rem(titleFontSize) : titleFontSize }}>
                        {title}
                    </h2>
                )}
                {message && (
                    <pre id={descId} style={styles.message}>
                        {message}
                    </pre>
                )}
            </div>
            {actionText && onAction && (
                <button
                    aria-hidden={!title}
                    autoFocus
                    style={{
                        ...styles.btn,
                        background: btnBackground,
                        color: btnColor,
                        cursor: 'pointer',
                    }}
                    onClick={onAction}>
                    {actionText}
                </button>
            )}
            {children}
        </div>
    );
}
