import React from 'react';
import { useBoolean, useId } from '@fluentui/react-hooks';
import { Icon, IStyle, mergeStyles } from '@fluentui/react';
import { CollapsedClassName, useCollapsingAreaStyles } from './CollapsingArea.styles';

export interface ICollapsingAreaProps {
    title: string | JSX.Element;
    farItems?: JSX.Element;
    collapseIcon: string;
    expandIcon: string;
    collapse?: boolean;
    titleClassName?: string;
    styles?: CollapsingAreaStyles;
}

export interface CollapsingAreaStyles {
    button?: IStyle | string;
    icon?: IStyle | string;
    text?: IStyle | string;
    collapsibleRegion?: IStyle | string;
}

export const CollapsingArea: React.FC<ICollapsingAreaProps> = ({
    title,
    collapse,
    collapseIcon,
    expandIcon,
    children,
    titleClassName,
    farItems,
    styles,
}: React.PropsWithChildren<ICollapsingAreaProps>): JSX.Element => {
    const [collapsedState, { toggle }] = useBoolean(collapse ?? false);
    const collapsingAreaStyles = useCollapsingAreaStyles();
    const ariaControlsId = useId('collapsible-region-');

    return (
        <div className={collapsedState ? CollapsedClassName : 'is-expanded'}>
            <div className={mergeStyles(collapsingAreaStyles.title, titleClassName)}>
                <button
                    className={mergeStyles(collapsingAreaStyles.button, styles?.button)}
                    onClick={toggle}
                    aria-expanded={!collapsedState}
                    aria-controls={ariaControlsId}>
                    <Icon
                        className={mergeStyles(collapsingAreaStyles.icon, styles?.icon)}
                        iconName={collapsedState ? collapseIcon : expandIcon}
                    />
                    <div className={mergeStyles(collapsingAreaStyles.text, styles?.text)}>{title}</div>
                </button>
                {farItems}
            </div>

            <div
                id={ariaControlsId}
                className={mergeStyles(collapsingAreaStyles.collapsibleRegion, styles?.collapsibleRegion)}>
                {children}
            </div>
        </div>
    );
};
