export type TestRequest = (request: Request) => boolean;
export const isGet: TestRequest = request => request.method === 'GET';

export type TestResponse = (response: Response) => boolean;
export const isServerErrorOrTooManyRequests: TestResponse = response =>
    response.status >= 500 || response.status === 429;

export type TestRequestAndResponse = (request: Request, response?: Response) => boolean;
export const isGetAndOk: TestRequestAndResponse = (request, response) =>
    request.method === 'GET' && (response?.ok ?? true);

export type RequestSelector = (request: Request) => PropertyKey;
export const urlSelector: RequestSelector = request => request.url;
