import { IPanelStyles } from '@fluentui/react';
import { isDarkTheme } from '@addressbook/components-fluentui-teams-theme';
import { makeStyles } from '../styles/makeStyles';

export const usePanelStyles = /* #__PURE__*/ makeStyles<Partial<IPanelStyles>>(theme => ({
    main: {
        animation: 'none',
        backgroundColor: isDarkTheme(theme.name) ? theme.palette.neutralLight : undefined,
    },
    scrollableContent: {
        flexDirection: 'column',
        width: '100%',
        height: '100%',
        display: 'flex',
    },
    commands: {
        padding: 0,
        borderBottom: `1px solid ${theme.palette.neutralTertiaryAlt}`,
        alignItems: 'center',
        justifyContent: 'flex-start',
    },
    navigation: {
        height: 'auto',
    },
    content: {
        padding: '24px 4px',
        flex: '1 1 auto',
        overflowY: 'auto', // fallback for FireFox
        'overflow-y': 'overlay', // make scrollbar float over content
    },
    contentInner: {
        height: '100%',
    },
}));
