export enum PeopleSelectionType {
    User,
    Group,
    Tag,
    Unspecific,
}

export interface BasePeopleSelection {
    id: string;
    name: string;
    mail?: string;
    icon?: string;
    unremovable?: boolean;
}

export interface SinglePeopleSelection extends BasePeopleSelection {
    type: PeopleSelectionType.User;
}

export interface ManyPeopleSelection extends BasePeopleSelection {
    type: PeopleSelectionType.Group | PeopleSelectionType.Tag | PeopleSelectionType.Unspecific;
    items?: OrPromise<SinglePeopleSelection[]>;
    resolveItems?: () => OrPromise<SinglePeopleSelection[]>;
    count?: number;
    expandable?: boolean;
}

export type PeopleSelection = SinglePeopleSelection | ManyPeopleSelection;

export type RetrieveIcon = (selection: PeopleSelection) => OrPromise<PeopleSelection['icon'] | Blob>;

export type WithSelection<T> = T & { selection: PeopleSelection };

type OrPromise<T> = T | Promise<T>;

export type FormatOptionalStringBySelection = (selection: PeopleSelection) => string | undefined;
