import type { app } from '@microsoft/teams-js';
import React, { useCallback, useContext, useEffect, useState } from 'react';
import { coerceTeamsTheme, TeamsContextTheme } from '../themes';
import { getTeamsContext, registerOnThemeChangeHandler } from '../wrappers';

interface LoadingTeamsHostValue {
    loading: true;
}

interface LoadedTeamsHostValue {
    loading: false;
    context: app.Context;
}

export type TeamsHostValue = LoadingTeamsHostValue | LoadedTeamsHostValue;

const defaultTeamsHostValue: TeamsHostValue = { loading: true };
const TeamsReactContext = React.createContext<TeamsHostValue>(defaultTeamsHostValue);

export interface TeamsHostProviderProps {
    value?: TeamsHostValue;
    defaultValue?: TeamsHostValue;
}

/**
 * The component that provides the Teams host access to its children.
 *
 * @property value The controlled value to provide to its children.
 * @property defaultValue The default value to provide to its children.
 */
export const TeamsHostProvider: React.FC<TeamsHostProviderProps> = ({
    value,
    defaultValue = defaultTeamsHostValue,
    children,
}) => {
    const [providingValue, setProvidingValue] = useState<TeamsHostValue>(value ?? defaultValue);

    const update = useCallback(async () => setProvidingValue({ loading: false, context: await getTeamsContext() }), []);
    const watch = useCallback(async () => {
        try {
            await update();
            await registerOnThemeChangeHandler(() => {
                update();
            });
        } catch {
            // No-op
        }
    }, [update]);

    // Only update the context if the controlled value is not provided.
    const controlled = Boolean(value);
    useEffect(() => {
        if (controlled) return;
        watch();
    }, [controlled, watch]);

    return <TeamsReactContext.Provider value={providingValue}>{children}</TeamsReactContext.Provider>;
};

/**
 * A hook to get the Teams context.
 *
 * @returns The Teams context, or `undefined` if the host is not ready yet.
 */
export function useTeamsContext(): app.Context | undefined {
    const value = useContext(TeamsReactContext);
    if (value.loading) return;

    return value.context;
}

/**
 * A hook to get the Teams theme.
 *
 * @returns The Teams theme, or `undefined` if the host is not ready yet.
 */
export function useTeamsTheme(): TeamsContextTheme | undefined {
    const value = useContext(TeamsReactContext);
    if (value.loading) return;

    return coerceTeamsTheme(value.context.app.theme);
}
