import React from 'react';
import { mergeStyles } from '@fluentui/react';
import { CommonIcon, CommonIconSize } from '../CommonIcon';
import { useCommonCardStyles } from './CommonCard.styles';

export interface CommonCardProps {
    accentColor: string;
    iconName: string;
    title: string;
    description?: React.ReactNode;
    highlightDescription?: boolean;
    reminder?: JSX.Element;
    isMobile?: boolean;
    farItem?: React.ReactNode;

    // If expandable, not mobile, and there's no reminder, the icon will be larger and centerized
    // vertically, and the description is able to wrap.
    expandable?: boolean;
    focusable?: boolean;
    id?: string;
    role?: React.AriaRole;
    onCardClick?: () => void;
}

export const CommonCard: React.FC<CommonCardProps> = (props: CommonCardProps): JSX.Element => {
    const {
        accentColor,
        iconName,
        title,
        description,
        highlightDescription,
        reminder,
        isMobile,
        expandable,
        id,
        role,
        farItem,
        onCardClick,
    } = props;
    const cardStyles = useCommonCardStyles();
    const expanded = expandable && !isMobile && !reminder;

    return (
        <div role={role}>
            <button
                id={id}
                className={mergeStyles(
                    cardStyles.root,
                    expanded && cardStyles.expandedRoot,
                    isMobile && cardStyles.mobileRoot,
                    isMobile && !reminder && cardStyles.mobileRootWithoutReminder
                )}
                onClick={onCardClick}>
                <CommonIcon
                    icon={iconName}
                    size={expanded ? CommonIconSize.Size64 : CommonIconSize.Size32}
                    iconBackgroundColor={accentColor}
                    title={title}
                    description={description}
                    rightItem={farItem}
                    styles={{
                        root: expanded && cardStyles.expandedIconRoot,
                        description: mergeStyles([
                            highlightDescription && cardStyles.hightlightedDescription,
                            expanded && cardStyles.expandedDescription,
                        ]),
                    }}
                />
                {reminder}
            </button>
        </div>
    );
};
