import { IStyle, mergeStyles } from '@fluentui/react';
import React from 'react';
import { userSlideDotsStyles } from './CommonSlideDots.styles';

export interface CommonSlideDotsProps {
    amount: number;
    activeIndex: number;
    isClickable?: boolean;
    slideDotsStyles?: SlideDotsStyles;
    onClick?: (x: number) => void;
}

export interface SlideDotsStyles {
    selectOption?: IStyle | string;
}

export const CommonSlideDots = ({
    amount,
    activeIndex,
    isClickable = false,
    slideDotsStyles,
    onClick,
}: CommonSlideDotsProps): JSX.Element => {
    const styles = userSlideDotsStyles();

    return (
        <div className={styles.stepper}>
            {Array(amount)
                .fill(0)
                .map((_item, index) => (
                    <i
                        className={mergeStyles(
                            styles.selectOption,
                            slideDotsStyles?.selectOption,
                            isClickable && 'is-clickable',
                            index === activeIndex ? 'is-active' : ''
                        )}
                        key={index}
                        onClick={isClickable ? () => onClick?.(index) : undefined}
                        data-testid='option'
                    />
                ))}
        </div>
    );
};
