import React from 'react';
import { mergeStyles } from '@fluentui/react';
import { ReactComponent as SpinnerSvg } from '../assets/spinner.svg';
import { useSpinnerStyles } from './Spinner.styles';

export enum SpinnerSize {
    Small = 24,
    Medium = 32,
    Large = 48,
}

export interface SpinnerProps extends React.HTMLAttributes<HTMLDivElement> {
    size?: SpinnerSize | number;
}

export const Spinner: React.FC<SpinnerProps> = ({ size = SpinnerSize.Medium, className, ...props }) => {
    const styles = useSpinnerStyles();

    return (
        <div className={mergeStyles(styles.root, className)} role='progressbar' aria-busy {...props}>
            <div className={mergeStyles(styles.bound, { width: size, height: size })}>
                <SpinnerSvg className={styles.animated} />
            </div>
        </div>
    );
};
