import { ProgressIndicator } from '@fluentui/react';
import React from 'react';
import { useLoadingIndicatorStyles } from './CommonLoadingIndicator.styles';

export interface CommonLoadingIndicatorProps {
    isFetching?: boolean;
}

export const CommonLoadingIndicator: React.FC<CommonLoadingIndicatorProps> = ({ isFetching }): JSX.Element => {
    const styles = useLoadingIndicatorStyles();

    return <ProgressIndicator progressHidden={!isFetching} styles={styles} />;
};
