import { ServiceOperation, ServiceResourceLocator } from '@addressbook/services-core';
import { DirectoryObject, Presence, User } from '@microsoft/microsoft-graph-types';
import { ProfilePhotoSize } from '../types';
import { GraphDriveResource } from './drive';
import { GraphProfilePhotoResource } from './profile-photo';

/**
 * The user resource.
 *
 * @see https://learn.microsoft.com/graph/api/resources/user
 */
export class GraphUserResource {
    constructor(private operation: ServiceOperation, private locator: ServiceResourceLocator) {}

    /**
     * Navigates to the drive resource.
     *
     * @returns The drive resource.
     * @see https://learn.microsoft.com/graph/api/drive-get
     */
    get drive(): GraphDriveResource {
        return new GraphDriveResource(this.operation, this.locator.extend('drive'));
    }

    /**
     * Navigates to the profile photo resource.
     *
     * @returns The profile photo resource.
     * @see https://learn.microsoft.com/graph/api/profilephoto-get
     */
    get photo(): GraphProfilePhotoResource {
        return new GraphProfilePhotoResource(this.operation, this.locator.extend('photo'));
    }

    /**
     * Navigates to the profile photo resource by size.
     *
     * @param size The profile photo size.
     * @returns The profile photo resource.
     * @see https://learn.microsoft.com/graph/api/profilephoto-get
     */
    photos(size: ProfilePhotoSize): GraphProfilePhotoResource {
        return new GraphProfilePhotoResource(this.operation, this.locator.extend('photos', `${size}x${size}`));
    }

    /**
     * Get Presence of the user.
     *
     * @returns The presence of the user.
     * @see https://learn.microsoft.com/en-us/graph/api/presence-get
     */
    async getPresence(signal?: AbortSignal): Promise<string> {
        const url = `/v1.0/${this.locator.path}/presence`;
        const response = await this.operation.requestJson<Presence>(url, { signal });

        return response.availability ?? 'PresenceUnknown'; // Possible values are Available, AvailableIdle, Away, BeRightBack, Busy, BusyIdle, DoNotDisturb, Offline, PresenceUnknown
    }

    /**
     * Get Presence of the user.
     *
     * @returns The presence of the user.
     * @see https://learn.microsoft.com/en-us/graph/api/orgcontact-get-manager
     */
    async getManager(signal?: AbortSignal): Promise<User> {
        const url = `/v1.0/${this.locator.path}/manager`;
        const response = await this.operation.requestJson<DirectoryObject>(url, { signal });
        console.log('url:', url);
        console.log('response:', response);
        return response;
    }

    /**
     * Gets my site URL of the user.
     *
     * @param signal The abort signal.
     * @returns The my site URL.
     * @see https://learn.microsoft.com/graph/api/user-get
     */
    async getMySite(signal?: AbortSignal): Promise<string> {
        const url = `/v1.0/${this.locator.path}?$select=mySite`;
        const response = await this.operation.requestJson<User>(url, { signal });
        return response.mySite ?? '';
    }
}
