import { isDarkTheme, isDefaultTheme } from '@addressbook/components-fluentui-teams-theme';
import { makeStyles } from '../styles/makeStyles';

export const useTaskModuleV2Styles = /* #__PURE__*/ makeStyles({
    root: {
        width: '100%',
        height: '100%',
        overflowY: 'auto',
        display: 'flex',
        flexDirection: 'column',
        boxSizing: 'border-box',
        paddingBottom: '0.75rem',
    },
    content: {
        padding: '24px 60px',
    },
    header: {
        marginBottom: '22px',
    },
});

export const useTaskModuleContentCustomizedStyles = /* #__PURE__*/ makeStyles(theme => ({
    background: isDefaultTheme(theme.name)
        ? 'linear-gradient(#DCE0FA 1%,white 40%) fixed'
        : isDarkTheme(theme.name)
        ? 'linear-gradient(#3D3E78 1%,#333333 40%) fixed'
        : 'black',
}));

export const useTaskModuleCategorizedTemplateStoreStyles = /* #__PURE__*/ makeStyles(() => ({
    padding: '24px 0',
}));
