import { resizeTaskModule } from '@addressbook/components-metaos-host';
import { useEffect } from 'react';
import { noop } from '../utils';

export interface TeamsTaskModuleSize {
    width: number;
    height: number;
}

/**
 * Default size of teams dialog.
 *
 * @see /Source/Clients/web-apps/WorkReport/manifests/teams.template.json
 */
export const defaultSize: TeamsTaskModuleSize = { width: 1000, height: 700 };

export function useTeamsTaskModuleSize(size: TeamsTaskModuleSize = defaultSize): void {
    useEffect(() => update({ width: size.width, height: size.height }), [size.width, size.height]);
    useEffect(() => () => update(defaultSize), []);
}

function update(size: TeamsTaskModuleSize): void {
    resizeTaskModule(size).catch(noop);
}
