import React from 'react';

/**
 * A unique key for a rendering.
 *
 * @remarks `number` is intentionally not used since React keys are converted to strings eventually,
 * even if they are numbers, which can lead to unexpected behavior when unrendering elements with
 * numeric keys.
 */
export type RenderingKey = string;
export type RenderingElement = React.ReactElement;

/**
 * The result of the `useRenderings` hook.
 *
 * @property renderings The current renderings.
 * @property render Renders a new element.
 * @property unrender Unrenders an element.
 * @property dispose Disposes all renderings.
 */
export interface UseRenderingsResult {
    renderings: RenderingElement[];
    render: (key: RenderingKey, element: RenderingElement) => void;
    unrender: (key: RenderingKey) => void;
    dispose: () => void;
}

/**
 * A hook that allows to dynamically render and unrender React elements.
 */
export function useRenderings(): UseRenderingsResult {
    const [renderings, setRenderings] = React.useState<UseRenderingsResult['renderings']>([]);

    const render = React.useCallback<UseRenderingsResult['render']>(
        (key, element) =>
            setRenderings(renderings => {
                let index = renderings.findIndex(rendering => rendering.key === key);
                if (index < 0) index = renderings.length;

                const newRenderings = [...renderings];
                newRenderings[index] = React.cloneElement(element, { key });
                return newRenderings;
            }),
        [setRenderings]
    );

    const unrender = React.useCallback<UseRenderingsResult['unrender']>(
        key => setRenderings(rendering => rendering.filter(element => element.key !== key)),
        [setRenderings]
    );

    const dispose = React.useCallback<UseRenderingsResult['dispose']>(() => setRenderings([]), [setRenderings]);

    return { renderings, render, unrender, dispose };
}
