import { makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useMobileSheetStyles = makeStyles(theme => ({
    content: {
        overflow: 'auto',
        maxHeight: 'calc(100vh - 54px)',
        minHeight: '90px', // for A11y - voice control overlay show number in iOS Safari.
    },
    header: {
        height: '20px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    handler: {
        width: '36px',
        height: '4px',
        background: theme.palette.neutralTertiaryAlt,
        borderRadius: '2px',
    },
}));

export const useMobileSheetContainerStyles = makeStyles(theme => ({
    background: theme.palette.white,
    paddingBottom: '8px',
}));

export const useMobileSheetBackdropStyles = makeStyles({ border: 'none' });
