/**
 * A semaphore is a synchronization primitive that restricts the number of simultaneous accesses to a shared resource.
 */
export class Semaphore {
    private currentInternal: number;
    private notifies: VoidFunction[];

    /**
     * Create a semaphore.
     *
     * @param resources The number of resources.
     */
    constructor(resources: number) {
        if (resources <= 0 || !Number.isInteger(resources)) throw new Error('Resources must be a positive integer.');

        this.currentInternal = resources;
        this.notifies = [];
    }

    /**
     * Get the number of available resources.
     */
    get current(): number {
        return this.currentInternal;
    }

    /**
     * Acquire a resource.
     *
     * @returns A promise that resolves to an idempotent function to release the resource.
     */
    async acquire(): Promise<VoidFunction> {
        while (this.currentInternal <= 0) await new Promise<void>(resolve => this.notifies.push(resolve));
        this.currentInternal--;

        let released = false;
        return () => {
            if (released) return;
            released = true;

            this.currentInternal++;
            this.notifies.shift()?.();
        };
    }

    /**
     * Run an action with a resource.
     *
     * @param action The action to run.
     * @returns The result of the action.
     */
    async run<T>(action: () => T | Promise<T>): Promise<T> {
        const release = await this.acquire();
        try {
            return await action();
        } finally {
            release();
        }
    }
}
