/**
 * Silently resolves a promise.
 *
 * @param promise The promise.
 * @returns The promise that resolves if the promise resolves, and resolves undefined if the promise rejects.
 */
export async function silently(promise: Promise<void>): Promise<void>;
export async function silently<T>(promise: Promise<T>): Promise<T | undefined>;
export async function silently<T>(promise: Promise<T>): Promise<T | undefined> {
    try {
        return await promise;
    } catch {
        // Ignore the error.
    }
}

/**
 * Delay for a number of milliseconds.
 *
 * @param ms The number of milliseconds to delay.
 * @returns A promise that resolves after the delay.
 */
export function delay(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms));
}
