import { FontWeights } from '@fluentui/react';
import { FontSizes, isContrastTheme, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const usePillPivotStyles = makeStyles(_theme => ({
    root: {
        display: 'flex',
        gap: '4px',

        '&.is-mobile': {
            gap: '8px',
        },
    },
}));

export const usePillButtonStyles = makeStyles(theme => ({
    root: {
        boxSizing: 'border-box',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '81px',
        height: '25px',
        cursor: 'pointer',
        borderRadius: '30px',
        fontWeight: FontWeights.regular,
        fontSize: FontSizes.size12,
        border: `1px solid ${isContrastTheme(theme.name) ? theme.palette.themeDark : theme.palette.neutralLight}`,
        background: theme.palette.white,
        color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.neutralPrimary,

        padding: 0,
        '&::before, &::after': {
            display: 'none',
        },

        '&.is-active': {
            background: isContrastTheme(theme.name) ? theme.palette.themePrimary : theme.palette.themeLighter,
            borderColor: isContrastTheme(theme.name) ? theme.palette.themeDark : theme.palette.themeLight,
            color: isContrastTheme(theme.name) ? theme.palette.black : theme.palette.themePrimary,
            fontWeight: FontWeights.semibold,
        },
    },
    mobile: {
        boxSizing: 'border-box',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '105px',
        height: '32px',
        cursor: 'pointer',
        borderRadius: '20px',
        fontWeight: FontWeights.regular,
        fontSize: FontSizes.size16,
        background: theme.palette.neutralLighter,
        color: theme.palette.neutralDark,

        padding: 0,
        '&::before, &::after': {
            display: 'none',
        },

        '&.is-active, &.is-selected:hover': {
            background: theme.palette.themePrimary,
            color: theme.palette.white,
            fontWeight: FontWeights.semibold,
        },
    },
    content: {
        lineHeight: 'initial',
    },
}));
