import { useEffect, useRef } from 'react';

/**
 * A Hook to create an object URL for a blob.
 * This will revoke the previous object URL when the blob changes or the component unmounts.
 *
 * @param blob The Blob to create an object URL for.
 * @returns The object URL for the Blob.
 */
export function useBlobObjectUrl(blob: Blob): string;
export function useBlobObjectUrl(blob: undefined): undefined;
export function useBlobObjectUrl(blob: Blob | undefined): string | undefined;
export function useBlobObjectUrl(blob: Blob | undefined): string | undefined {
    const blobRef = useRef<Blob>();
    const objectUrlRef = useRef<string>();

    const updateObjectUrlRef = useRef((objectUrl?: string) => {
        const previousObjectUrl = objectUrlRef.current;
        if (previousObjectUrl) URL.revokeObjectURL(previousObjectUrl);
        objectUrlRef.current = objectUrl;
    });

    if (blob !== blobRef.current) {
        blobRef.current = blob;
        updateObjectUrlRef.current(blob ? URL.createObjectURL(blob) : undefined);
    }

    useEffect(() => updateObjectUrlRef.current, []);
    return objectUrlRef.current;
}
