import { uuid } from '@addressbook/utilities-core';
import {
    ILogger,
    IScenarioEventData,
    IScenarioLogger,
    TelemetryScenarioStatus,
    TelemetryScenarioStep,
} from './common/interfaces';

/**
 * There are some scenarios that will end in the service side, not in the client side.
 * Most notably, there is a platform limitation in which closing task module interrupts the logger calls.
 */
export class ScenarioLogger<TScenarioName extends string, TStepName extends string, TLogCategory extends string>
    implements IScenarioLogger<TStepName>
{
    private id: string;
    private name: string;
    private eventData: IScenarioEventData;
    private logger: ILogger<TScenarioName, TStepName, TLogCategory>;
    private startTime: number;
    private isScenarioComplete: boolean;
    public elapsedTime: number;

    constructor(logger: ILogger<TScenarioName, TStepName, TLogCategory>, name: string, dataBag?: any) {
        this.id = uuid();
        this.name = name;
        this.logger = logger;
        this.isScenarioComplete = false;
        this.startTime = this.elapsedTime = performance.now();
        this.eventData = {
            elapsed: 0,
        };

        this.createMark(TelemetryScenarioStep.Start, TelemetryScenarioStatus.Init, { dataBag }, false);
    }

    public mark(stepName: TStepName, status?: TelemetryScenarioStatus, eventData?: Partial<IScenarioEventData>): void {
        this.createMark(stepName, status ?? TelemetryScenarioStatus.Incomplete, eventData, false);
    }

    public stop(eventData?: Partial<IScenarioEventData>): void {
        this.createMark(
            TelemetryScenarioStep.Finish,
            (eventData && eventData['Scenario.Status']) || TelemetryScenarioStatus.Success,
            eventData,
            true
        );
    }

    public fail(eventData?: Partial<IScenarioEventData>): void {
        this.createMark(TelemetryScenarioStep.Finish, TelemetryScenarioStatus.Failure, eventData, true);
    }

    public getCurrentElapsedTime(): number {
        return performance.now() - this.elapsedTime;
    }

    private createMark(
        stepName: string,
        status: TelemetryScenarioStatus,
        eventData: Partial<IScenarioEventData> | undefined | null,
        completeScenario: boolean
    ): void {
        if (!this.isScenarioComplete) {
            this.isScenarioComplete = completeScenario;

            const timestamp = performance.now();

            if (eventData) {
                this.eventData = { ...this.eventData, ...eventData };
            }

            this.eventData.id = this.id;
            this.eventData['Scenario.Step'] = stepName;
            this.eventData['Scenario.Status'] = status;
            this.eventData.elapsed = this.elapsedTime = timestamp - this.startTime;
            this.eventData.stepElapsed = eventData?.stepElapsed;

            this.logger.logScenario(this.name, this.eventData, completeScenario);
        } else {
            console.warn(
                `Scenario: ${this.name}, Step: ${stepName}
                is being called after scenario has completed.`
            );
        }
    }
}
