import { DebounceSettings, DebouncedFunc, debounce } from 'lodash-es';
import { DependencyList, useCallback, useEffect } from 'react';

/**
 * A hook to debounce a function, which will be flushed on unmount.
 *
 * @param func The function to debounce
 * @param deps The deps which will make the return func change
 * @param wait The number of milliseconds to delay.
 * @param options The options object.
 * @returns The debounced function.
 */
export function useDebounced<T extends (...args: never[]) => unknown>(
    func: T,
    deps: DependencyList,
    wait?: number,
    options?: DebounceSettings
): DebouncedFunc<T> {
    // eslint-disable-next-line react-hooks/exhaustive-deps
    const callback = useCallback(debounce(func, wait, options), deps);
    useEffect(() => callback.flush, [callback]);
    return callback;
}
