import React from 'react';
import { mergeStyles } from '@fluentui/react';
import { ModalContent, ModalContentProps } from '../CommonModal/ModalContent';
import { useTaskModuleV2Styles } from './TaskModuleV2.styles';

export type TaskModuleV2Props = Omit<ModalContentProps, 'onDismiss'>;
export const TaskModuleV2: React.FC<TaskModuleV2Props> = props => {
    const styles = useTaskModuleV2Styles();
    const contentMergedStyles = mergeStyles(styles.content, props.contentClassName);
    const initialProps: TaskModuleV2Props = {
        contentClassName: contentMergedStyles,
        headerClassName: styles.header,
    };

    return (
        <div className={styles.root}>
            <ModalContent {...props} {...initialProps} />
        </div>
    );
};
