import { keyframes } from '@fluentui/react';
import { makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const useSpinnerStyles = makeStyles({
    root: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    bound: {
        overflow: 'hidden',
    },
    animated: {
        display: 'block',
        willChange: 'transform',
        animation: `${keyframes({
            from: { transform: 'translateY(0)' },
            to: { transform: 'translateY(-100%)' },
        })} 2s steps(60) infinite`,
    },
});
