import { FontWeights } from '@fluentui/react';
import { FontSizes, makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const CollapsedClassName = 'is-collapsed';
export const useCollapsingAreaStyles = makeStyles(theme => ({
    title: {
        height: '2rem',
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        cursor: 'pointer',
        boxSizing: 'border-box',
    },
    button: {
        display: 'flex',
        alignItems: 'center',
        textAlign: 'start',
        padding: 0,
        border: 'none',
        background: 'transparent',
    },
    icon: {
        height: '100%',
        paddingRight: '4px',
        color: theme.palette.black,
    },
    text: {
        color: theme.palette.black,
        fontSize: FontSizes.size12,
        fontWeight: FontWeights.bold,
        flex: 1,
    },
    collapsibleRegion: {
        [`.${CollapsedClassName} &`]: {
            display: 'none',
        },
    },
}));
