import { ServiceOperation, ServiceResourceLocator } from '@addressbook/services-core';
import { GraphDriveItemResource } from './drive-item';

/**
 * The share resource.
 *
 * @see https://learn.microsoft.com/graph/api/resources/shareddriveitem
 */
export class GraphShareResource {
    constructor(private operation: ServiceOperation, private locator: ServiceResourceLocator) {}

    /**
     * Navigates to the drive item resource.
     *
     * @returns The drive item resource.
     * @see https://learn.microsoft.com/graph/api/driveitem-get
     */
    get driveItem(): GraphDriveItemResource {
        return new GraphDriveItemResource(this.operation, this.locator.extend('driveItem'));
    }
}
