import React from 'react';
import { ModalContent, ModalContentProps } from '../CommonModal/ModalContent';
import { useTaskModuleStyles } from './TaskModule.styles';

export type TaskModuleProps = Omit<ModalContentProps, 'onDismiss'>;
export const TaskModule: React.FC<TaskModuleProps> = props => {
    const styles = useTaskModuleStyles();
    return (
        <div className={styles.root}>
            <ModalContent {...props} />
        </div>
    );
};
