/* istanbul ignore file */

import { createTheme, ITheme } from '@fluentui/theme';
import { CoreColors, DarkColors, ContrastColors, DefaultV2Colors, DarkV2Colors } from './teams-colors';
import { FontSizes } from './teams-sizes';

const iosBaseFont = '-apple-system, BlinkMacSystemFont';
const andoridBaseFontFont = 'Roboto';
export const IOS_FONT_FAMILY = `${iosBaseFont}, sans-serif`;
export const ANDROID_FONT_FAMILY = `${andoridBaseFontFont}, sans-serif`;

const defaultFontStyle = {
    fontFamily: `${iosBaseFont}, "Segoe UI", system-ui, "Apple Color Emoji", "Segoe UI Emoji", ${andoridBaseFontFont} sans-serif`,
};

const fonts = {
    tiny: { fontSize: FontSizes.size10 },
    xSmall: { fontSize: FontSizes.size10 },
    small: { fontSize: FontSizes.size12 },
    smallPlus: { fontSize: FontSizes.size12 },
    medium: { fontSize: FontSizes.size14 },
    mediumPlus: { fontSize: FontSizes.size16 },
    large: { fontSize: FontSizes.size18 },
    xLarge: { fontSize: FontSizes.size20 },
    xLargePlus: { fontSize: FontSizes.size24 },
    xxLarge: { fontSize: FontSizes.size28 },
    xxLargePlus: { fontSize: FontSizes.size32 },
    superLarge: { fontSize: FontSizes.size42 },
    mega: { fontSize: FontSizes.size68 },
};

/**
 * Workaround for the missing colors in the palette of fluentui v8.
 * TODO: Remove this workaround once migrate to fluentui v9.
 */
declare module '@fluentui/theme' {
    interface IPalette {
        redLight?: string;
    }
}

export const TeamsThemeDefault: ITheme = createTheme({
    defaultFontStyle,
    fonts,
    palette: {
        black: CoreColors.appBlack,
        neutralDark: CoreColors.appGray02,
        neutralPrimary: CoreColors.appGray03,
        neutralPrimaryAlt: CoreColors.appGray03,
        neutralSecondary: CoreColors.appGray04,
        neutralTertiary: CoreColors.appGray06,

        neutralTertiaryAlt: CoreColors.appGray08,
        neutralQuaternary: CoreColors.appGray09,
        neutralQuaternaryAlt: CoreColors.appGray09,
        neutralLight: CoreColors.appGray10,
        neutralLighter: CoreColors.appGray12,
        neutralLighterAlt: CoreColors.appGray14,
        white: CoreColors.appWhite,

        // themeDark & ThemeDarkAlt has same color values, themeLighter & themeLighterAlt has same color values
        themeDarker: CoreColors.appBrand02,
        themeDark: CoreColors.appBrand04,
        themeDarkAlt: CoreColors.appBrand04,
        themePrimary: CoreColors.appBrand06,
        themeSecondary: CoreColors.appBrand08,
        themeTertiary: CoreColors.appBrand12,
        themeLight: CoreColors.appBrand14,
        themeLighter: CoreColors.appBrand16,
        themeLighterAlt: CoreColors.appBrand16,

        red: CoreColors.appRed,
        redLight: CoreColors.appRedLight,
        redDark: CoreColors.appRedDark,
        green: CoreColors.appGreen,
        greenLight: CoreColors.appGreenLight,
        greenDark: CoreColors.appGreenDark,
    },
    semanticColors: {
        inputText: CoreColors.appBrand06,
        inputBackground: CoreColors.appBrand16,
    },
});
TeamsThemeDefault.name = 'default';

