import { ServiceOperation, ServiceOperationOptions, ServiceResourceLocator } from '@addressbook/services-core';
import { GraphRootResource } from './resources';

export type GraphServiceClientOptions = ServiceOperationOptions;

/**
 * A service client for the Microsoft Graph API.
 *
 * @see https://learn.microsoft.com/graph/api/overview
 */
export class GraphServiceClient extends GraphRootResource {
    constructor(operationOptions: GraphServiceClientOptions = {}) {
        super(new ServiceOperation(operationOptions), ServiceResourceLocator.root);
    }
}
