import { makeStyles } from '@addressbook/components-fluentui-teams-theme';

export const usePeoplePileStyles = makeStyles({
    root: {
        display: 'flex',
        alignItems: 'center',
    },
    coins: {
        display: 'flex',
        alignItems: 'center',
        gap: '4px',
    },
    compactCoins: {
        gap: 0,
        '>:not(:first-child)': {
            marginInlineStart: '-2px',
        },
    },
    overflow: {
        zIndex: 0,
        margin: '0 4px',
        '::before': {
            content: '"+"',
        },
    },
    compactOverflow: {
        margin: '0 2px',
    },
    onlyName: {
        margin: '0 4px',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
    },
});
