import React from 'react';
import { IStyle, Text, Icon, mergeStyles } from '@fluentui/react';
import { useKeyboardInvoke } from '@addressbook/utilities-hooks';
import { useCommonIconStyles } from './CommonIcon.styles';

export interface CommonIconProps {
    /**
     * icon name or icon element
     * - IconName or string as iconName
     * - Any JSX.Element as rendered icon
     */
    icon?: string | JSX.Element;
    iconColor?: string;
    iconBackgroundColor?: string;
    size?: CommonIconSize;
    styles?: CommonIconStyles;

    title?: string;
    titleId?: string;
    titleAs?: React.ElementType<React.HTMLAttributes<HTMLElement>>;
    description?: React.ReactNode;
    descriptionId?: string;

    editIconAriaLabel?: string;
    onIconClick?: () => void;
    className?: string;

    titleTrailingItem?: React.ReactNode;
    rightItem?: React.ReactNode;
}

export interface CommonIconStyles {
    root?: IStyle;
    content?: IStyle;
    editIcon?: IStyle;
    title?: IStyle;
    description?: IStyle;
    textarea?: IStyle;
    iconContainer?: IStyle;
}

/**
 * size of icon container
 */
export enum CommonIconSize {
    Size28 = 'icon-size-28',
    Size32 = 'icon-size-32',
    Size40 = 'icon-size-40',
    Size44 = 'icon-size-44',
    Size48 = 'icon-size-48',
    Size64 = 'icon-size-64',
    Size80 = 'icon-size-80',
}

export const CommonIcon: React.FC<CommonIconProps> = ({
    icon,
    iconColor = 'white',
    iconBackgroundColor,
    size = CommonIconSize.Size32,
    styles = {},
    title,
    titleId,
    titleAs,
    description,
    descriptionId,
    onIconClick,
    editIconAriaLabel,
    className,
    titleTrailingItem,
    rightItem,
}: CommonIconProps): JSX.Element => {
    const defaultStyles = useCommonIconStyles();

    const ref = useKeyboardInvoke<HTMLDivElement>();

    return (
        <div className={mergeStyles(defaultStyles.root, styles.root, className)}>
            <div className={mergeStyles(size, defaultStyles.content, styles.content)}>
                {icon && (
                    <div
                        role={onIconClick ? 'button' : 'presentation'}
                        tabIndex={onIconClick ? 0 : undefined}
                        onClick={onIconClick}
                        ref={ref}
                        aria-label={onIconClick && editIconAriaLabel}
                        className={mergeStyles(
                            defaultStyles.iconContainer,
                            iconBackgroundColor && {
                                backgroundColor: iconBackgroundColor,
                            },
                            styles.iconContainer
                        )}>
                        {typeof icon === 'string' ? (
                            <Icon
                                className={mergeStyles(defaultStyles.icon, {
                                    color: iconColor,
                                    fill: iconColor,
                                })}
                                iconName={icon}
                            />
                        ) : (
                            icon
                        )}
                        {onIconClick && (
                            <Icon className={mergeStyles(defaultStyles.editIcon, styles.editIcon)} iconName='Edit' />
                        )}
                    </div>
                )}
                {(title || description) && (
                    <div className={mergeStyles(defaultStyles.textContainer, styles.textarea)}>
                        {title && (
                            <div className={defaultStyles.titleContainer}>
                                <Text
                                    id={titleId}
                                    title={title}
                                    role='presentation'
                                    // prevent screen reader from reading the title twice (group) in MacOS
                                    // about presentation role: https://developer.mozilla.org/en-US/docs/Web/Accessibility/ARIA/ARIA_Techniques/Using_the_presentation_role
                                    className={mergeStyles(defaultStyles.title, styles.title)}
                                    as={titleAs}>
                                    {title}
                                </Text>
                                {titleTrailingItem}
                            </div>
                        )}
                        {description &&
                            (typeof description === 'string' ? (
                                <Text
                                    id={descriptionId}
                                    title={description}
                                    role='presentation'
                                    className={mergeStyles(defaultStyles.description, styles.description)}>
                                    {description}
                                </Text>
                            ) : (
                                description
                            ))}
                    </div>
                )}
            </div>
            {rightItem}
        </div>
    );
};
